import React, { Component } from 'react'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal'
import { connect } from 'react-redux';
import { GET_LIST_ALL_PIN_TO_TOP_POST } from '../../../../../reducer/keys';
import { GET_PIN_TO_TOP_LIST, GET_REORDER_PIN_TO_TOP } from '../../../../../actions/moderationActions';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import ReactHtmlParser from 'react-html-parser';
import { Card, Image, OverlayTrigger, Tooltip } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../utils'
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import ImageType from '../../CommonComponents/MediaType/ImageType'
import Author from '../../CommonComponents/Author/Author'
import { Loader } from '../../CommonComponents/Loader';

const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed); return result;
};

const getItemStyle = (isDragging, draggableStyle) => ({ ...draggableStyle });
const getListStyle = isDraggingOver => ({});

class ChangeOrderModal extends Component {
    constructor(props) {
        super(props);
        this.state = { items: [], newArray: [], spinner: false, updateOrder: false };
        this.onDragEnd = this.onDragEnd.bind(this);
    }
    componentDidMount() { this.onLoadData(this.props); }


    reArrangePosts = () => {
        const { newArray, updateOrder } = this.state;
        const postId = newArray.map(item => item.id);
        const indexing = newArray.map((_, index) => index + 1);
        if (updateOrder) {
            this.setState({ spinner: true })
            this.props.GET_REORDER_PIN_TO_TOP(postId, indexing,
                setTimeout(() => {
                    this.setState({ spinner: false })
                }, 1000)

            )
        }

    };


    componentDidUpdate(prevProps) {
        if (prevProps.openpinToTopModal !== this.props.openpinToTopModal) {
            if (this.props.openpinToTopModal) this.props.GET_PIN_TO_TOP_LIST()
        }
        if (prevProps.pinToTop !== this.props.pinToTop) this.onLoadData(this.props)
    }


    onLoadData = (props) => {
        const { pinToTop } = props;
        this.setState({ items: pinToTop, newArray: pinToTop });
    }

    onDragEnd(result) {
        if (!result.destination) { return; }
        const items = reorder(this.state.items, result.source.index, result.destination.index);
        this.setState({ items, newArray: items, updateOrder: true });
    }
    render() {
        const { openpinToTopModal, closeModalPop } = this.props
        const { spinner } = this.state
        return (

            <Modal className='pintotop_modal' size="lg" show={openpinToTopModal} onHide={closeModalPop} centered>
                <Modal.Header closeButton >
                    <Modal.Title>Change Order</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className='mx-xl-4 px-lg-4 py-lg-2 mx-0 overflow-auto' style={{ height: 500 }}>
                        {this.state.items && this.state.items?.length > 0 ?
                            <DragDropContext onDragEnd={this.onDragEnd} >
                                <Droppable droppableId="droppable">
                                    {(provided, snapshot) => (
                                        <div {...provided.droppableProps} ref={provided.innerRef} style={getListStyle(snapshot.isDraggingOver)}>
                                            {this.state.items && this.state.items.length && this.state.items.map((item, index) => (
                                                <Draggable key={`item-${index}`} draggableId={`item-${index}`} index={index}>
                                                    {(provided, snapshot) => (
                                                        <div ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps} style={getItemStyle(snapshot.isDragging, provided.draggableProps.style)}>
                                                            <Card data-id={item.id} className='mb-4'>
                                                                <Card.Body className='p-2'>
                                                                    <span className='position-absolute start-0 translate-middle h-18px w-18px rounded-circle f-center bg-dark text-gray-100 top-50 fs-10 fw-medium' style={{ zIndex: 1 }}>{index + 1}</span>
                                                                    <div className="d-flex flex-lg-row flex-column">
                                                                        <div className='f-center h-100 text-gray-300 me-2 fs-6'>
                                                                            <FontAwesomeIcon type={'solid'} icon={'grip-dots-vertical'} />
                                                                        </div>
                                                                        {item.type != 1 ? <div className='d-flex flex-shrink-0 flex-column cursor-pointer pe-lg-4 mx-auto mb-lg-0 mb-5' style={{ width: 120 }}>
                                                                            <ImageType mediaClass={'position-relative rounded-1 overflow-hidden w-100'} size={100} height={120} width={120} url={item.postFile || item.newMedia} alt={item.postname} isCover={true} />
                                                                        </div> : null}
                                                                        <div className="d-flex flex-grow-1 flex-column pl-lg-5 w-100 overflow-hidden">
                                                                            <div className="d-flex align-items-start mb-1">
                                                                                <Link href={item.link} target='_blank' className="d-flex align-items-start me-2 flex-grow-1 overflow-hidden text-decoration-none text-gray-800" title={item.postname}>
                                                                                    <Author className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                                                        imageSize={40}
                                                                                        symbolClass={'symbol-label fw-bolder fs-5'}
                                                                                        name={item.postname} profileUrl={item.picture} networkId={item.networkId} />
                                                                                    <div className="pe-2 w-100 overflow-hidden">
                                                                                        <p className=" mb-0"><strong className="text-break text-ellipsis w-100 d-block">{item.postname}</strong></p>
                                                                                        <p className="mb-1 text-gray-500 text-break fs-9">@{item.postname}</p>
                                                                                    </div>
                                                                                </Link>
                                                                                <span className="d-flex align-items-center h-20px w-20px flex-shrink-0">
                                                                                    <OverlayTrigger placement="top" overlay={<Tooltip>{GET_NETWORK_NAME(item.networkId)}</Tooltip>}>
                                                                                        <Image className="h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.networkId)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(item.networkId)} data-invert={GET_NETWORK_NAME(item.networkId)} title={GET_NETWORK_NAME(item.networkId)} fluid />
                                                                                    </OverlayTrigger>
                                                                                </span>
                                                                            </div>
                                                                            <p className='line-clamp line-clamp-3 mb-1'>{ReactHtmlParser(item.postcontent)}</p>
                                                                        </div>
                                                                    </div>
                                                                </Card.Body>
                                                            </Card>
                                                        </div>
                                                    )}
                                                </Draggable>
                                            ))}
                                        </div>
                                    )}
                                </Droppable>
                            </DragDropContext>
                            :
                            <Loader />}

                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant='secondary' className='ms-3 btn-min-w' onClick={closeModalPop}>Cancel</Button>
                    <Button className={`ms-3 btn-min-w ${spinner ? 'spinner' : ''}`} onClick={this.reArrangePosts}>Done</Button>
                </Modal.Footer>
            </Modal>
        )
    }
}


const mapStateToProps = state => {
    return {
        pinToTop: state.moderation?.pinToTop
    }
}


const mapDispatchToProps = dispatch => {
    return {

        GET_LIST_ALL_PIN_TO_TOP_POST: () => dispatch(GET_LIST_ALL_PIN_TO_TOP_POST()),
        GET_PIN_TO_TOP_LIST: () => dispatch(GET_PIN_TO_TOP_LIST()),
        GET_REORDER_PIN_TO_TOP: (postId, indexing, calback) => dispatch(GET_REORDER_PIN_TO_TOP(postId, indexing, calback))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ChangeOrderModal);

