import { Nav, Badge, Container, Dropdown, NavDropdown } from 'react-bootstrap'
import Filters from './Elements'
import ModSubHeader from './Elements/ModSubHeader'

const ModHeader = (props) => {
    const isLoaded = props.moderation.details && Object.keys(props.moderation.details).length > 0 ? true : false
    var {activeTab, totalpost, publicpost, privatepost , tabName, TabTotal } = 0
    var upcomingpost = []
    if(isLoaded){
        activeTab = props.activeTab
        totalpost = props.moderation.details.totalpost
        publicpost = props.moderation.details.publicpost
        privatepost = props.moderation.details.privatepost
        upcomingpost = props.moderation.details.upcomingpost
        tabName = activeTab == 0 ? "All Posts" : activeTab == 1 ? "Public" : activeTab == 2 ? "Private" : "Upcoming" 
        TabTotal = activeTab == 0 ? totalpost : activeTab == 1 ? publicpost : activeTab == 2 ? privatepost : upcomingpost 
    }

    return <div className="dualheader subheader">
                <Container className="f-between flex-wrap border-bottom" fluid>
                    <div className="d-flex align-items-center flex-wrap h-50px">
                        <Nav variant="underline" className='d-none d-lg-flex'>
                            <Nav.Item>
                                <Nav.Link style={{padding:'14px 0px'}} className={props.activeTab ==0 ? 'active' :''} eventKey="allPost" onClick={isLoaded ? props.clickToTab(0) : null} disabled={!isLoaded}>
                                    All Posts
                                    <Badge bg="" className={`m-0 py-1 ms-2 ${activeTab == 0 ? "badge-primary":"badge-light-primary"}`}>{totalpost}</Badge> 
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link style={{padding:'14px 0px'}} className={props.activeTab ==1 ? 'active' :''} eventKey="public" onClick={isLoaded ? props.clickToTab(1) : null} disabled={!isLoaded}>
                                    Public
                                    <Badge bg="" className={`m-0 py-1 ms-2 ${activeTab == 1 ? "badge-primary":"badge-light-primary"}`}>{publicpost}</Badge> 
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link style={{padding:'14px 0px'}} className={props.activeTab ==2 ? 'active' :''} eventKey="private" onClick={isLoaded ? props.clickToTab(2) : null} disabled={!isLoaded}>
                                    Private
                                    <Badge bg="" className={`m-0 py-1 ms-2 ${activeTab == 2 ? "badge-primary":"badge-light-primary"}`}>{privatepost}</Badge> 
                                </Nav.Link>
                            </Nav.Item>
                            {upcomingpost && upcomingpost > 0 ? <Nav.Item>
                                <Nav.Link style={{padding:'14px 0px'}} className={props.activeTab ==3 ? 'active' :''} eventKey="upcomming">
                                    Upcoming
                                    <Badge bg="" className={`m-0 py-1 ms-2 ${activeTab == 3 ? "badge-primary":"badge-light-primary"}`}>{upcomingpost}</Badge> 
                                </Nav.Link>
                            </Nav.Item>:null}
                        </Nav>
                        <Dropdown className='d-flex d-lg-none'>
                            <Dropdown.Toggle variant="secondary" size='sm' id="mod-tab">
                            {tabName} <Badge bg="" className={"m-0 py-1 badge-light-primary mx-1"}>{TabTotal}</Badge>
                            </Dropdown.Toggle>
                            <Dropdown.Menu>
                                <NavDropdown.Item eventKey="1" onClick={isLoaded ? props.clickToTab(0) : null} >All Posts <Badge className='ms-2 rounded-3' bg={"light-primary"}>{totalpost}</Badge> </NavDropdown.Item>
                                <NavDropdown.Item eventKey="2" onClick={isLoaded ? props.clickToTab(1) : null} >Public <Badge className='ms-2 rounded-3' bg={"light-primary"}>{publicpost}</Badge> </NavDropdown.Item>
                                <NavDropdown.Item eventKey="3" onClick={isLoaded ? props.clickToTab(2) : null}>Private <Badge className='ms-2 rounded-3' bg={"light-primary"}>{privatepost}</Badge> </NavDropdown.Item>
                                {upcomingpost && upcomingpost > 0 ? <NavDropdown.Item eventKey="4" onClick={isLoaded ? props.clickToTab(3) : null} >Upcoming <Badge className='ms-2 rounded-3' bg={"light-primary"}>{upcomingpost}</Badge> </NavDropdown.Item>:null}
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>
                    <div className="d-flex align-items-center flex-wrap flex-grow-1 justify-content-end">
                        <Filters {...props} />
                    </div>
                </Container>
                <ModSubHeader {...props} />
            </div>
}
export default ModHeader;

