import React, { Component, useEffect, useState } from 'react'
import { OverlayTrigger, Tooltip } from 'react-bootstrap'
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import { ExpiredPostInstaQueue } from '../../../../../actions/moderationActions'
import { isRefresh } from '../../../../../utils'
class RefreshPostData extends Component {
    state = {
        isRequest: false
    }
    componentDidMount() {
        const { isRequest } = this.state
        const { item } = this.props
        if (isRefresh(item)) {
            this.setState({ isRequest: true }, () => ExpiredPostInstaQueue(item))
        }
    }
    render() {
        const { refreshPostLoading, onRefreshPost, item } = this.props
        return <OverlayTrigger placement="top" overlay={<Tooltip>Refresh Post</Tooltip>} >
            <span className={`refresh_post_media_${item.id} me-2 cursor-pointer ${refreshPostLoading ? "disabled" : ""}`} onClick={() => onRefreshPost(item)} style={{ display: 'none' }}>
                <span className={`refresh-icon-${item.id} d-inline-block`}><FontAwesomeIcon icon={'arrows-rotate'} /></span>
            </span>
        </OverlayTrigger>
    }
}

export default RefreshPostData