import React, { Component } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useCookies, withCookies } from 'react-cookie';
import { TAGGSHOP_SOCIAL_ACCOUNT_DATA } from '../../../../../../actions/sceneActions';
import { Dropdown, Form, InputGroup, Button, OverlayTrigger, Tooltip, Image } from "react-bootstrap";
import { showAddAccounntAlert } from './utils';
import { useNavigate } from 'react-router';
import { addAccountFromId } from '../../../../../../utils/addAccount';
import { getTwitterAuth } from '../../../../../../utils/auth';
import Author from '../../../CommonComponents/Author/Author';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';


const channel = new BroadcastChannel('connect');

const SocialAccounts = (props) => {
  const [cookies, setCookie] = useCookies([]);
  const navigation = useNavigate()
  const dispatch = useDispatch()
  const socialAccountsData = useSelector(state => state.userAccountData.socialAccounts)
  return <SAccounts {...props} cookies={cookies} navigation={navigation} dispatch={dispatch} socialAccountsData={socialAccountsData} />
}
class SAccounts extends Component {
  state = { socialAccount: 0, authorName: null, authorAvatar: null, errorMsg: false, otherAc: false, accountDetails: {}, accountList: [], selectedPersonalAccount: null }

  handleMessage = async (event) => {
    try {
      const response = JSON.parse(event.data);
      const { code } = response;
      const networkId = localStorage.getItem('networkId');
      const type = localStorage.getItem('connectionType');
      if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
        if (type == 'moderation-ugc') {
          if (code) {
            if (networkId == 1) {
              await getTwitterAuth(code, (response) => {
                if (response && Object.keys(response).length > 0) this.filterAccountData(networkId, response);
              })
            } else {
              await addAccountFromId(networkId, code, () => this.props.dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA(null, () => this.filterAccountData(networkId, null))));
            }
          }
        }
      }
    } catch (error) {

    }
  };

  componentDidMount() {
    this.filterAccountData(2, null);
    document.cookie = "callBackUrl=React";
    window.addEventListener("message", this.handleMessage);
    channel.addEventListener("message", this.handleMessage);
    if (window.Application) window.Application.reInit()
    if (this.props.networkId == parseInt(JSON.parse(localStorage.getItem('instagram_ugc'))?.networkId)) {
      this.setState({ accountDetails: JSON.parse(localStorage.getItem('instagram_ugc')) })
    }
    else this.setState({ accountDetails: null })
  }

  filterAccountData = (id, data) => {
    const { socialAccountsData, setUserName } = this.props
    let allAcc = []
    socialAccountsData?.accountInformations?.forEach(acc => {
      if (acc.Network.id == id && acc.ConnectedAccounts) {
        acc.ConnectedAccounts.value2 = acc.ConnectedAccounts.accountName;
        allAcc.push(acc);
      }
    });
    if (id == 1 && data) {
      allAcc.unshift({
        ConnectedAccounts: {
          id: data.id_str,
          value2: data.name,
          profilePicture: data.picture
        },
        Network: {
          color: "#0F1419",
          description: "Twitter",
          icon: "fa-twitter",
          id: 1,
          name: "Twitter",
          status: 1
        }
      });
    }


    this.setState({
      accountList: allAcc,
      selectedPersonalAccount: allAcc[0] || ''
    }, () => setUserName(allAcc[0]?.ConnectedAccounts.value2 || ''));
  };


  sendRequest = () => {
    const { setStep, setUserName } = this.props
    if (this.state.accountDetails) {
      setUserName(this.state.accountDetails.username)
      setStep(3)
    }
    else { this.setState({ errorMsg: true }) }
  }

  nextStep = () => {
    const { setStep } = this.props
    if (this.state.selectedPersonalAccount) setStep(3)
    else { this.setState({ errorMsg: true }) }
  }

  componentWillUnmount() {
    localStorage.removeItem("networkId")
    window.removeEventListener("message", this.handleMessage);
    channel.removeEventListener("message", this.handleMessage);
  }



  render() {
    const { setStep, networkId, setUserName } = this.props
    const { errorMsg, accountList, selectedPersonalAccount } = this.state
    return (
      <>
        <div className="p-lg-6 p-4 py-3 rights_modal_aside_wrap">
          <div className="ugc_steps ugc-logged-ac modal-body">
            <>
            {selectedPersonalAccount ? 
            <Dropdown style={{ minWidth: 220 }} id="connectedAll">
              <Dropdown.Toggle  variant="secondary" size={'sm'} className={`d-flex align-items-center w-100 justify-content-between ${errorMsg ? 'is-invalid border-danger' : ''}`}>
                <div className="d-flex align-items-center flex-grow-1">
                  <Author className={'symbol symbol-30 symbol-circle flex-shrink-0 me-2'}
                      imageSize={30}
                      symbolClass={'symbol-label fs-8'}
                      name={selectedPersonalAccount ? selectedPersonalAccount?.ConnectedAccounts?.accountName :''} 
                      profileUrl={selectedPersonalAccount ? selectedPersonalAccount?.ConnectedAccounts?.profilePicture : ''} networkId={selectedPersonalAccount ? selectedPersonalAccount?.ConnectedAccounts?.networkId :''} />
                  <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                      <p className="fw-medium mb-0 text-ellipsis fs-8">{selectedPersonalAccount ? selectedPersonalAccount?.ConnectedAccounts?.accountName :''}</p>
                  </div>
                </div>
              </Dropdown.Toggle>

              <Dropdown.Menu className="w-100">
                <div className="navi flex-column navi-hover py-2 px-5 connectedAllAccounts">Select Account:</div>
                {accountList.map(acc => {        
                  return <Dropdown.Item key={acc.ConnectedAccounts.id} onClick={() => { this.setState({ selectedPersonalAccount: acc }, () => setUserName(acc.ConnectedAccounts.value2)) }}>
                      <div className="d-flex align-items-center">
                          <Author className={'symbol symbol-30 symbol-circle flex-shrink-0 me-2'}
                              imageSize={30}
                              symbolClass={'symbol-label fs-8'}
                              name={acc.ConnectedAccounts.value2} profileUrl={acc.ConnectedAccounts?.profilePicture} networkId={acc.ConnectedAccounts?.networkId} />
                          <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                              <p className="fw-medium mb-0 text-ellipsis fs-8">{acc.ConnectedAccounts.value2}</p>
                          </div>
                      </div>
                  </Dropdown.Item>
                })}
                <Dropdown.Item onClick={() => { localStorage.setItem('connectionType', 'moderation-ugc'); showAddAccounntAlert(networkId) }}>
                  <div className="d-flex align-items-center">
                    <Author className={'symbol symbol-30 symbol-circle flex-shrink-0 me-2'}
                        imageSize={30}
                        symbolClass={'symbol-label fs-8'}
                        name={''} profileUrl={baseUrl('media/images/error/no-profile.jpg')} networkId={''} />
                    <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                        <p className="fw-medium mb-0 text-ellipsis fs-8">Add Another Account</p>
                    </div>
                  </div>
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown> :

            <Button variant="secondary" size={'sm'} className={`d-flex align-items-center w-100 justify-content-between ${errorMsg ? 'is-invalid border-danger' : ''}`} onClick={() => {localStorage.setItem('connectionType', 'moderation-ugc'); showAddAccounntAlert(networkId)}}>
              <div className="d-flex align-items-center flex-grow-1">
                <Author className={'symbol symbol-30 symbol-circle flex-shrink-0 me-2'}
                    imageSize={30}
                    symbolClass={'symbol-label fs-8'}
                    name={''} 
                    profileUrl={baseUrl('media/images/error/no-profile.jpg')} networkId={networkId} />
                <div className="overflow-hidden f-between w-100 pe-3" >
                    <p className="fw-medium mb-0 text-ellipsis fs-8">Add Account</p>
                    <FontAwesomeIcon icon={'plus'} />
                </div>
              </div>
            </Button>
            }
            {errorMsg ? <span id="social_account_error" className="invalid-feedback">
              Please connect account
            </span> : null}
            </>
          </div>
          <span id="reSearch_cookies" onClick={() => this.getCookieData()}></span>
        </div>
        <div className="d-flex justify-content-end p-4 border-top">
          <Button variant="secondary" className="me-2" onClick={() => setStep(1)}>Back</Button>
          <Button onClick={() => this.nextStep()}>Next Step</Button>
        </div>
      </>
    )
  }
}
export default withCookies(SocialAccounts)