    import React, { useEffect } from "react";
    import Draggable from "react-draggable";
    import { OverlayTrigger, Tooltip } from "react-bootstrap";

    const Hotspot = ({
      ImageClass,
      item,
      enableHotspot,
      activeCarousalPostsList,
      activeCarousalPost,
      spotWidth,
      spotHeight,
      onControlledDrag,
      changePrdId,
      ctaData,
    }) => {
      useEffect(() => {}, [activeCarousalPostsList?.length ? activeCarousalPostsList : item.postproduct]);

      const getCarousal = activeCarousalPostsList?.length
        ? item?.imageList?.find((e) => e.referenceId === activeCarousalPost)
        : null;


      const renderHotspots = (hotspotList) =>
        hotspotList.map((Item, index) => {
          const left = (spotWidth * Item.hotspot.left.replace("%", "")) / 100;
          const top = (spotHeight * Item.hotspot.top.replace("%", "")) / 100;

          return (
            <Draggable
              bounds="parent"
              onStop={onControlledDrag}
              key={`hotspot_${Item.ugc_product}`}
              onStart={() => changePrdId(Item.ugc_product)}
              defaultPosition={{ x: left, y: top }}
            >
              <div className="h-16px w-16px position-absolute">
                <OverlayTrigger
                  placement="top"
                  overlay={
                    <Tooltip data-theme="light">
                      <div className="text-start">
                        {Item.product?.product_title || ""}
                        {((Item.product?.product_discount > 0) || Item.product?.product_price > 0) && (
                          <div className="fw-bold fs-6">
                            {Item.product?.price_currency_symbol}
                            {Item.product?.product_discount > 0
                              ? Item.product.product_discount
                              : Item.product?.product_price}
                          </div>
                        )}
                      </div>
                    </Tooltip>
                  }
                >
                  <div
                    className={`blob ${ctaData?.hotspot_type === 1 ? "blob_002" : "blob_001"}`}
                    style={{ backgroundColor: ctaData?.hotspot_color }}
                  >
                    <div
                      className="f-center fw-medium fs-8"
                      style={{ color: ctaData?.hotspot_type_color, lineHeight: "20px" }}
                    >
                      {ctaData?.hotspot_type === 1 ? index + 1 : ""}
                    </div>
                  </div>
                </OverlayTrigger>
              </div>
            </Draggable>
          );
        });


      return (
        <div className={`h-100 w-100 ${ImageClass}`}>
          {enableHotspot == 1 && spotWidth ? (
            activeCarousalPostsList?.length && getCarousal?.hotspot
              ? renderHotspots(activeCarousalPostsList)
              : item.postproduct?.length && !activeCarousalPost
              ? renderHotspots(item.postproduct)
              : null
          ) : null}
        </div>
      );
    };

    export default Hotspot;
