import React, { Component } from 'react'
import { autoCompleteSearch } from '../../../../../../actions/moderationActions'
import { Button, InputGroup, Form, Dropdown } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper'


class QuickSearch extends Component {
    state = { tagSearchList: [], searchPost: "", selected: "", openSearch: false }

    onLoad = () => {
        const { searchPost } = this.state;
        if (searchPost.length > 0) {
            autoCompleteSearch(searchPost).then((response) => {
                const { responseData, totalTag, responseCode } = response.data;
                if (responseCode === 200) this.setState({ tagSearchList: responseData?.totalTag })
                else this.setState({ tagSearchList: [] })
            })
        }
        else { this.setState({ tagSearchList: [] }) }
    }

    onSearchText = (text) => this.setState({ searchPost: text }, () => this.onLoad())


    onClickToSearchTags = (selectSearch) => event => {
        this.setState({ searchPost: selectSearch, selected: selectSearch, tagSearchList: [] }, () => this.props.searchTagsFilter(this.state.selected))
    }

    onClickToSearchPost = (selectSearch) => event => {
        if (selectSearch && String(selectSearch).length > 0) this.setState({ searchPost: '', selected: selectSearch, tagSearchList: [] }, () => this.props.searchPostFilter(selectSearch))
    }

    render() {
        const { tagSearchList, openSearch, searchPost } = this.state;
        return <>
            <Button variant='icon' size='sm' aria-label='search' className="btn-active-primary" onClick={() => this.setState({ openSearch: !openSearch })}>
                <FontAwesomeIcon icon={openSearch ? 'xmark' : 'magnifying-glass'} />
            </Button>
            <Dropdown show={tagSearchList && tagSearchList.length > 0 ? true : false} className="assets_search_input position-absolute me-lg-2" style={{ display: openSearch ? 'block' : 'none' }}>
                <div className="quick_search d-flex align-items-center">
                    <InputGroup id="inputSpinner" className="p-0 spinner-gray spinner-sm">
                        {openSearch && <Form.Control size='sm' style={{ minHeight: '32px' }} className='' id="assets_search_input" placeholder="What are you looking for?" value={searchPost} onChange={(e) => this.onSearchText(e.target.value)} autoComplete='off' />}
                        <Button className='' size='sm' id="inputCloseButton" aria-label='search' variant="primary" onClick={this.onClickToSearchPost(searchPost)}>
                            <FontAwesomeIcon icon="magnifying-glass" />
                        </Button>
                    </InputGroup>
                    {(tagSearchList && tagSearchList.length > 0) ? <Dropdown.Menu className="w-100 dropdwon-height top-100" style={{ overflowY: 'auto' }}>
                        {
                            tagSearchList.map((item) => <Dropdown.Item key={item} onClick={this.onClickToSearchTags(item)}>{item}</Dropdown.Item>)
                        }
                    </Dropdown.Menu> : null}
                </div>
            </Dropdown>

        </>
    }
}

export default QuickSearch;