import React, { Component } from 'react'
import ReactApexChart from 'react-apexcharts'


class RadialChart extends React.Component {
    constructor(props) {
        super(props);
        var days = (30 - props.ago) * 3.33;
        this.state = {
            series: [days],
            options: {
                chart: {
                    height: 320,
                    type: "radialBar"
                },
                plotOptions: {
                    radialBar: {
                        hollow: {
                            margin: 0,
                            size: "65%",
                        },
                        dataLabels: {
                            showOn: "always",
                            name: {
                                show: true,
                                fontWeight: '700',
                                name: "TEST"
                            },
                            value: {
                                color: ["#545454"],
                                fontSize: "30px",
                                fontWeight: '700',
                                offsetY: 12,
                                show: false
                            }
                        },
                        track: {
                            background: ["#f2f6f8"],
                            strokeWidth: '100%'
                        },
                        dataLabels: {
                            show: true,
                            name: {
                                show: true,
                                fontSize: '16px',
                                fontFamily: 'inter',
                                fontWeight: 600,
                                color: '#545454',
                                offsetY: -10
                            },
                            value: {
                                show: false,
                                fontSize: '14px',
                                fontFamily: 'inter',
                                fontWeight: 800,
                                color: '#545454',
                                offsetY: 16
                            },
                            total: {
                                show: true,
                                label: props.ago + "Days",
                                color: '#373d3f',
                                fontSize: '16px',
                                fontFamily: 'inter',
                                fontWeight: 800
                            }
                        }
                    }
                },
                colors: ["#ffa800"],
                stroke: {
                    lineCap: "round",
                }
            }

        };
    }

    componentDidUpdate(prevProps){
        if(prevProps.ago !== this.props.ago){
            var days = (30 - this.props.ago) * 3.33;
            this.setState({  
                    series: [days],
                    options: {
                        chart: {
                            height: 320,
                            type: "radialBar"
                        },
                        plotOptions: {
                            radialBar: {
                                hollow: {
                                    margin: 0,
                                    size: "65%",
                                },
                                dataLabels: {
                                    showOn: "always",
                                    name: {
                                        show: true,
                                        fontWeight: '700',
                                        name: "TEST"
                                    },
                                    value: {
                                        color: ["#545454"],
                                        fontSize: "30px",
                                        fontWeight: '700',
                                        offsetY: 12,
                                        show: false
                                    }
                                },
                                track: {
                                    background: ["#f2f6f8"],
                                    strokeWidth: '100%'
                                },
                                dataLabels: {
                                    show: true,
                                    name: {
                                        show: true,
                                        fontSize: '16px',
                                        fontFamily: 'inter',
                                        fontWeight: 600,
                                        color: '#545454',
                                        offsetY: -10
                                    },
                                    value: {
                                        show: false,
                                        fontSize: '14px',
                                        fontFamily: 'inter',
                                        fontWeight: 800,
                                        color: '#545454',
                                        offsetY: 16
                                    },
                                    total: {
                                        show: true,
                                        label: this.props.ago + "Days",
                                        color: '#373d3f',
                                        fontSize: '16px',
                                        fontFamily: 'inter',
                                        fontWeight: 800
                                    }
                                }
                            }
                        },
                        colors: ["#ffa800"],
                        stroke: {
                            lineCap: "round",
                        }
                    }
        
                
            });
        }
    }


    render() {
        return <ReactApexChart options={this.state.options} series={this.state.series} type="radialBar" height={350} />

    }
}

export default RadialChart;