import React from 'react';
import { Card } from 'react-bootstrap';
import { generateRandomArray } from '../../../../../utils';

const ModerationSkeleton = () => {
    return (
        <div className="row gx-2">
            {generateRandomArray(20).map((_, index) => (
                <article className='assetsItem' aria-labelledby={`skeleton-${index}`} key={index}>
                    <Card className='border-0'>
                        <section className="f-between flex-wrap flex-sm-nowrap border-bottom p-2">
                            <span className="d-flex">
                                <span className="sk_rect h-14px w-14px my-1 me-2"></span>
                                <span className="sk_rect h-14px w-14px my-1 me-2"></span>
                                <span className="sk_rect h-14px w-14px my-1 me-2"></span>
                                <span className="sk_rect h-14px w-14px my-1 me-2"></span>
                            </span>
                            <span className="sk_line h-10px w-20px"></span>
                        </section>
                        <section className="sk_img rounded-0 w-100 flex-shrink-0 overflow-hidden" style={{ paddingBottom: '100%' }}></section>
                        <section className="d-flex align-items-center p-3">
                            <div className="d-flex align-items-center me-2 flex-grow-1 overflow-hidden">
                                <div className="sk_img symbol symbol-36 rounded-circle flex-shrink-0 me-2 overflow-hidden"></div>
                                <div className="overflow-hidden w-100" style={{ maxWidth: '130px' }}>
                                    <p className="sk_line sk_line_100 sk_line_height_10 mb-2"></p>
                                    <span className="sk_line sk_line_50 sk_line_height_8 mb-0 d-block"></span>
                                </div>
                            </div>
                            <span className="d-flex align-items-end flex-column flex-shrink-0" style={{ width: '100px' }}>
                                <div className="sk_img symbol symbol-28 rounded-1 flex-shrink-0 me-2 overflow-hidden"></div>
                            </span>
                        </section>
                        <section className='d-flex align-items-center p-3 border-top'>
                            <div className='w-50 me-1'>
                                <div className="sk_line sk_line_100 h-34px rounded-1"></div>
                            </div>
                            <div className='w-50 ms-1'>
                                <div className="sk_line sk_line_100 h-34px rounded-1"></div>
                            </div>
                        </section>
                    </Card>
                </article>
            ))}
        </div>
    );
};

export default ModerationSkeleton;
