import React, { useEffect, useState } from 'react'
import QuickSearch from './QuickSearch'
import DateRange from './dateRange'
import { Button, Form } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import withRouter from '../../../../../routes/WithRouter';
import { checkPermission } from '../../../../../../actions/collaboratorActions';
import { useSelector } from 'react-redux';
import { TOAST_ERROR } from '../../../../../../utils';

const Filters = (props) => {

    const [isCarosal, setisCarosal] = useState()
    const filters = useSelector((state) => state.moderation?.filter);

    useEffect(() => {
        const foundItem = props.allWalls?.length && props.allWalls.find(item => item.id == props.params.id);
        setisCarosal(foundItem?.carouselPost || 0);
    }, [props]);

    const carousalError = () => {
        TOAST_ERROR('To enable the Carousel feature, please remove the Product Filter first.')
    }

    return <div className="d-flex flex-wrap">
        <div className='f-center me-2'>
            <Form.Label htmlFor='carousalStatus' className='me-2 mb-0 d-none d-sm-inline-block'>Carousal</Form.Label>
            <Form.Check className='mt-1' type='switch' aria-label="Carousal" id="carousalStatus" checked={isCarosal} onChange={(e) => filters.taggedPost ? carousalError() : props.postCarosal(Number(e.target.checked))} />
        </div>

        <button onClick={() => checkPermission('networkManager', 'manageFeed') ? props.navigate(`/content/managefeed/${localStorage.getItem('wallId')}`) : {}} className="btn btn-outline-primary text-nowrap btn-sm me-2">
            <FontAwesomeIcon icon={'list-check'} iconClass={'d-inline d-lg-none me-0 me-sm-1'} />
            <span className='d-none d-lg-inline'>Manage Feeds </span>
        </button>
        <QuickSearch {...props} />
        <DateRange {...props}>
            <Button variant="icon" size='sm' className="btn-active-primary ms-2" aria-label='calendar' id="calander_">
                <FontAwesomeIcon icon={'calendar-range'} />
            </Button>
        </DateRange>
        <Button variant="icon" size='sm' className="btn-active-primary ms-2" aria-label='filter' id="filter_aside">
            <FontAwesomeIcon icon={'filter'} />
        </Button>
    </div>
}

export default withRouter(Filters);