import ApprovedModal from '../../UgcRights/Status/ApprovedModal'
import Pendingng from '../../UgcRights/Status/PendingModal'
import RejectModal from '../../UgcRights/Status/RejectModal'

const UgcStatus = (props) => {
    const { ugcData, statusPopup, ugcRequestData, onHide, openugcModel } = props
    return (<>
        {statusPopup == 1 && ugcRequestData && ugcRequestData.username ? <Pendingng ugcData={ugcData} ugcRequestData={ugcRequestData} onHide={onHide} /> : null}
        {statusPopup == 3 && ugcRequestData && ugcRequestData.username ? <RejectModal ugcRequestData={ugcRequestData} ugcData={ugcData} onHide={onHide}  openugcModel={openugcModel} /> : null}
        {statusPopup == 2 && ugcRequestData && ugcRequestData.username ? <ApprovedModal ugcRequestData={ugcRequestData} onHide={onHide} /> : null}
    </>)
}
export default UgcStatus