import React, { Component, Suspense } from 'react';
import { Modal, Dropdown } from 'react-bootstrap';
import { Button } from 'react-bootstrap';
import { connect } from "react-redux";
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { ENABLE_HOTSPOT_ACTION, REMOVE_TAGGED_PRODUCT_ACTION, UPDATE_HOTSPOT_ACTION } from '../../../../../actions/moderationActions';
import { getUserInfo_afterChanges } from '../../../../../actions/loginActions';
import EditPost from './EditElements';
import Hotspot from './Elements/Hotspot';
import ModalMedia from './Elements/ModalMedia';
import './modal.scss';
import ErrorFound from '../../../../ErrorFound';
import { getImageWidthHeight } from '../../../../../utils';
import { checkPermission } from '../../../../../actions/collaboratorActions';
import CarousalPost from './CarousalPost';

const ContentModalAside = React.lazy(() => import('./Elements/ContentModalAside'));
class ContentModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            spotWidth: 0, spotHeight: 0, searchTag: '', selectedTag: [], productId: 0, enableHotspot: this.props.modalData?.hotspot, loading: false,
            winWidth: window.innerWidth,
            winHeight: window.innerHeight,
            paddingBottom: 100,
            mediaSize: { height: 400, width: 400 },
            editPost: false,
            isTaggModal: false,
            activeCarousalPost: 0,
            activeCarousalPostsList: [],
            carousalPostsList: [],
        }
        this.hotspotRef = React.createRef();
    }

    componentDidMount() {
        setTimeout(() => {
            const hotspotSection = this.hotspotRef.current;
            if (hotspotSection) this.setState({ spotWidth: hotspotSection.clientWidth, spotHeight: hotspotSection.clientHeight });
        }, 100);

        window.addEventListener('resize', this.handleResize);
        this.onMediaLoad(this.props.modalData);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    componentDidUpdate(prevProps) {
        if (this.props.modalData !== prevProps.modalData) {
            this.onMediaLoad(this.props.modalData);
            this.setState({ enableHotspot: this.props.modalData?.hotspot, activeCarousalPost: 0, activeCarousalPostsList: [], carousalPostsList: [], })
        }
    }

    handleResizeg = () => {
        this.setState({
            winWidth: window.innerWidth,
            winHeight: window.innerHeight,
        });
    };



    

    updateCarousalPost = (e, data, refId = 0, status) => {

        // status=1 add product,2=remove product

        const { modalData } = this.props

        const getFilteredData = (list) => list.filter((obj) => obj.post_id == e);

        if (data) this.setState({ carousalPostsList: data })

        if (status) {
            const getPostData =
                status == 2 ? data.filter((item) => item.id != refId) : [...data];
            if (status == 1) getPostData.push(refId)
            const filteredData = getFilteredData(getPostData);
            this.setState(
                {
                    carousalPostsList: getPostData,
                    activeCarousalPostsList: []
                },
                () => this.updateCarousalPostList(filteredData)
            );
        } else {

 
            const hotspotValue = modalData?.imageList?.find(item => item.referenceId == e);
            const filteredData = getFilteredData(data || this.state.carousalPostsList);

            this.setState(
                {
                    activeCarousalPost: e,
                   enableHotspot: hotspotValue?.hotspot || false,
                    activeCarousalPostsList: [],
                },
                () => this.updateCarousalPostList(filteredData)
            );
        }
    }

    updateCarousalPostList = (e) =>{
        
        setTimeout(() => {
            const hotspotSection = this.hotspotRef.current;
            if (hotspotSection) {
                this.setState({ spotWidth: hotspotSection.clientWidth, spotHeight: hotspotSection.clientHeight });
            }
        }, 100);

         this.setState({ activeCarousalPostsList: e })}

   

    updateHotspot = (productId, location_hotspot) => {

        const { carousalPostsList, activeCarousalPost } = this.state

        const updatedData = carousalPostsList?.length && carousalPostsList.map(item => (
            item.ugc_product == productId ?
                {
                    ...item, // Keep other properties unchanged
                    hotspot: location_hotspot,
                    product: {
                        ...item.product, // Spread the child object
                        hotspot: location_hotspot // Update the `age` value
                    }
                } : item
        ));

        this.updateCarousalPost(activeCarousalPost, updatedData)
    }

    onControlledDrag = (e, position) => {
        const { x, y } = position;
        const { spotWidth, spotHeight, productId, activeCarousalPost } = this.state;
        const { modalData } = this.props;
        let l = x * 100 / spotWidth;
        let t = y * 100 / spotHeight;
        const location_hotspot = { 'left': `${l}%`, 'top': `${t}%` }
        const hostpot = `left:${l}%;top:${t}%;`
        this.props.UPDATE_HOTSPOT_ACTION(productId, activeCarousalPost ? activeCarousalPost : modalData?.referenceId, location_hotspot, hostpot, activeCarousalPost ? this.updateHotspot : null)
    };


    changeHotspotStatus = (hotspotEnable, referenceId) => { 
        const { activeCarousalPost } = this.state;
        let isHotspotEnable = hotspotEnable == 1 ? 0 : 1;
        this.setState({ enableHotspot: isHotspotEnable })
        this.props.ENABLE_HOTSPOT_ACTION(isHotspotEnable, activeCarousalPost ? activeCarousalPost : referenceId, this.onSaveCallBack, activeCarousalPost);
    };

    changePrdId = (id) => {
        this.setState({ productId: id })
    }

    onSaveCallBack = () => { 

        const { activeCarousalPost } = this.state;
        const { modalData } = this.props;

        var enableHotspot = this.props.modalData?.hotspot

        if(activeCarousalPost){
            const getCarousal = modalData?.imageList?.find((e)=>e.referenceId==activeCarousalPost)
            enableHotspot = getCarousal.hotspot
        }
        
        this.setState({ enableHotspot: enableHotspot })
    }
    

    responceApi = () => {
        this.setState({ loading: false });
    }

    handleClose = () => this.props.modalCallback(false)
    editPostFun = (status) => this.setState({ editPost: !this.state.editPost, isTaggModal: status })


    onMediaLoad = async (modalData) => {
        if (modalData.mediaHeight && modalData.mediaWidth) {
            this.setState({ mediaSize: { height: modalData.mediaHeight, width: modalData.mediaWidth } })
        } else {
            try {
                const { width, height } = await getImageWidthHeight(modalData.newMedia);
                this.setState({ mediaSize: { height: height, width: width } })
            } catch (error) {
                this.setState({ mediaSize: { height: 100, width: 100 } })
            }
        }
    }
    editPostFun = (status) => checkPermission('contentGallery', 'editGallery', true) ? this.setState({ editPost: !this.state.editPost, isTaggModal: status }) : {}

    render() {
        const { modalToggle, modalData, onRefreshPost, filter, ctaData, postPostUpdate, currentIndex, clickToPin, clickToGetUgc, clickToDeletePost, clickToBlockUser, handleAllImageError } = this.props
        const { spotWidth, spotHeight, enableHotspot, winWidth, winHeight, mediaSize, editPost, isTaggModal, activeCarousalPost, activeCarousalPostsList, carousalPostsList } = this.state;
        const scale = Math.min((winWidth - 450) / mediaSize.width, (winHeight - 130) / mediaSize.height);
        const height = mediaSize.height * scale
        const width = mediaSize.width * scale
        const paddingBottom = (height * 100) / width

        return <Modal size={'full'} show={modalToggle} className="assets_modal p-0" backdrop={false}>
            <Modal.Header className="justify-content-between py-0">


                <span className='text-muted fw-medium'>{editPost ? 'Edit Post' : 'General Info'}</span>
                <div className='d-flex align-items-center h-100'>
                    {!editPost ?
                        <>
                            <div className="d-none d-lg-flex h-100">
                                {
                                    <>
                                        {[18, 2].includes(modalData?.networkId) ?
                                            <Button variant={'secondary'} className={`p-0 f-center border-0 me-3 btn-active-primary flex-shrink-0 refresh_post_media_Popup${modalData.id}`} aria-label='refresh' onClick={() => onRefreshPost(modalData)} style={{ display: 'none' }}>
                                                <FontAwesomeIcon type={'solid'} icon={'arrows-rotate'} iconClass={'me-1'} />
                                                Refresh Post
                                            </Button> : null}
                                        {(((modalData.networkId == 3 && modalData.feedFilter == 62) || modalData.networkId == 7 || (modalData.networkId == 18 && (modalData.feedFilter == 24 || modalData.feedFilter == 25 || modalData.feedFilter == 26))) && modalData.ugcstatus == null && modalData.type != 1) ?
                                            <Button variant={'secondary'} className="p-0 f-center border-0 me-3 text-muted btn-active-primary flex-shrink-0" onClick={clickToGetUgc(modalData)}>
                                                <FontAwesomeIcon type={'solid'} icon={'user-check'} iconClass={'me-1'} />
                                                Get Rights
                                            </Button> : null}
                                        <Button variant={'secondary'} className="p-0 f-center border-0 me-3 text-muted btn-active-primary flex-shrink-0" onClick={() => this.editPostFun(true)}>
                                            <FontAwesomeIcon type={'solid'} icon={'tag'} iconClass={'me-1'} />
                                            Tags
                                        </Button>
                                        <Button variant={'secondary'} className="p-0 f-center border-0 me-3 text-muted btn-active-primary flex-shrink-0" onClick={() => this.editPostFun(false)}>
                                            <FontAwesomeIcon type={'solid'} icon={'pen'} iconClass={'me-1'} />
                                            Edit Post
                                        </Button>
                                    </>
                                }
                            </div>
                            <Dropdown align="end" className="f-center position-relative ms-4 h-100">
                                <Dropdown.Toggle variant="secondary" aria-label='more' className="p-0 f-center border-0 me-3 text-muted btn-active-primary arrow_disabled h-100">
                                    <FontAwesomeIcon icon={'ellipsis'} iconClass={'me-1'} />More
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    {(modalData.ugcstatus != 1 || modalData.ugcstatus != 2) ?
                                        <Dropdown.Item onClick={clickToDeletePost(modalData, modalData.id, modalData.status, currentIndex, modalData.feedId, modalData.feedFilter)}>
                                            <FontAwesomeIcon type={'solid'} icon={'trash-can'} iconClass={'me-1'} />
                                            <span className="navi-text">Delete Post</span>
                                        </Dropdown.Item>
                                        : null}
                                    <Dropdown.Item onClick={clickToBlockUser(currentIndex, modalData.id, modalData.username, 'grid', localStorage.getItem('wallId'))}>
                                        <FontAwesomeIcon type={'solid'} icon={'ban'} iconClass={'me-1'} />
                                        <span className="navi-text">Block User</span>
                                    </Dropdown.Item>
                                    {modalData.link != '' && modalData.link != null ?
                                        <Dropdown.Item href={modalData.link} target="_blank">
                                            <FontAwesomeIcon type={'solid'} icon={'paper-plane'} iconClass={'me-1'} />
                                            <span className="navi-text">View Post</span>
                                        </Dropdown.Item>
                                        : <></>}
                                </Dropdown.Menu>
                            </Dropdown>
                        </>
                        : null}
                    <div className='ms-5 d-flex justify-content-end'>
                        <Button variant='close' className="me-0 text-muted btn-active-primary" onClick={this.handleClose}></Button>
                    </div>
                </div>
            </Modal.Header>
            <Modal.Body className="p-0 d-flex border-0 flex-lg-row flex-column">
                <div className="assets_modal_inner flex-column flex-grow-1 f-center p-lg-4 p-xxl-5 position-relative mb-lg-0 overflow-hidden">

                    <Button variant='icon' className="assets_modal_arrow_left rounded-circle position-absolute" onClick={this.props.slideAssets(currentIndex - 1)}>
                        <FontAwesomeIcon type={'solid'} icon={'arrow-left'} />
                    </Button>
                    <div className='w-100 position-relative' style={{ maxWidth: winWidth > 991 ? width : null }}>

                        {
                            modalData.imageList && Object.keys(modalData.imageList).length ?
                                <CarousalPost carousalPostsList={carousalPostsList} updateCarousalPost={this.updateCarousalPost} item={modalData} handleAllImageError={handleAllImageError} />
                                :
                                <ModalMedia modalData={modalData} handleAllImageError={handleAllImageError} />
                        }


                        {!(modalData.type == 3 || modalData.type == 5) ? <div className="position-absolute top-0 start-0 w-100 h-100" style={{ zIndex: 1 }} ref={this.hotspotRef}>
                            <Suspense fallback={<></>}>
                                <ErrorFound><Hotspot ImageClass={'hotspot-section'} activeCarousalPostsList={activeCarousalPostsList} activeCarousalPost={activeCarousalPost} ImageUrl={modalData?.newMedia} spotWidth={spotWidth} spotHeight={spotHeight} enableHotspot={enableHotspot} item={modalData} width={width} height={height} onControlledDrag={this.onControlledDrag} changePrdId={this.changePrdId} imgHeight={modalData?.mediaHeight} imgWidth={modalData?.mediaWidth} ctaData={ctaData} /></ErrorFound>
                            </Suspense>
                        </div> : null}
                    </div>

                    <Button variant='icon' className="assets_modal_arrow_right rounded-circle position-absolute" onClick={this.props.slideAssets(currentIndex + 1)}>
                        <FontAwesomeIcon type={'solid'} icon={'arrow-right'} />
                    </Button>
                </div>

                <div className="full_modal_sidebar position-relative">
                    {editPost ?
                        <EditPost handleClose={() => this.editPostFun(false)} modalData={modalData} filter={filter} isTaggModal={isTaggModal} handleAllImageError={handleAllImageError} />
                        :
                        <Suspense fallback={<></>}>
                            {modalData ? <ContentModalAside updateCarousalPost={this.updateCarousalPost} carousalPostsList={carousalPostsList} activeCarousalPostsList={activeCarousalPostsList} activeCarousalPost={activeCarousalPost} modalData={modalData} changeHotspotStatus={this.changeHotspotStatus} enableHotspot={enableHotspot} postPostUpdate={postPostUpdate} key={modalData?.id} modalCallback={this.props.modalCallback} /> : null}
                        </Suspense>
                    }
                </div>
            </Modal.Body>
        </Modal>
    }
}

const mapStateToProps = (state) => {
    return {
        ctaData: state.ctaData?.data,
        authorized: state.authorized,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        UPDATE_HOTSPOT_ACTION: (productId, item, referenceId, location_hotspot, hostpot, callBack) => dispatch(UPDATE_HOTSPOT_ACTION(productId, item, referenceId, location_hotspot, hostpot, callBack)),
        REMOVE_TAGGED_PRODUCT_ACTION: (product_sku, productId, referenceId) => dispatch(REMOVE_TAGGED_PRODUCT_ACTION(product_sku, productId, referenceId)),
        ENABLE_HOTSPOT_ACTION: (id, referenceId, callBack, isCarousal) => dispatch(ENABLE_HOTSPOT_ACTION(id, referenceId, callBack, isCarousal)),
        getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ContentModal);