import { Modal, Image, Button } from "react-bootstrap";
import { baseUrl } from "../../../../../../Assets/AssetsHelper";

const ErrorModal = ({ show, onHide }) => {
  return <Modal show={show} centered onHide={onHide}>
        <Modal.Body className="text-center p-7">
            <Image className="my-3" src={baseUrl('media/images/error/ugc-request.png')} height={206} width={500} fluid/>
            <p className="text-center my-5 fs-6 mx-auto fw-bold text-muted" style={{maxWidth:400}}>
              To send UGC Rights request in bulk, make sure you choose posts from a single user.
            </p>
            <Button className="btn-min-w" size="lg" onClick={onHide}>OK</Button>
        </Modal.Body>
      </Modal>
};

export default ErrorModal;
