import React, { Component } from 'react'
import HTMLReactParser from 'react-html-parser';
import moment from 'moment'
import { WASABI_PATH } from '../../../../../../actions/api'; 
import { DOWNLOAD_CERTIFICATE } from '../../../../../../actions/ugcAction';
import {saveAs} from "file-saver";
import { Modal, Button, Form, InputGroup, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import Author from '../../../CommonComponents/Author/Author';
import ShowMoreContent from '../../../CommonComponents/ShowMoreContent';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../../utils';
import ImageType from '../../../CommonComponents/MediaType/ImageType';
class UgcModalApproved extends Component {
    onDownloadMedia = (data) => {
        if (data.type != 5 && data.type != 3) {
            saveAs(data.postFile, `${data.id}`);
        }
        else{
            saveAs(data.mediaUrl, `${data.id}`);

        }

    }
    render() {
        const { ugcRequestData, ugcData, onHide } = this.props;
        return ugcRequestData ? <>

            <Modal size={'full'} show={true} className="assets_modal p-0" backdrop={false}>
                <Modal.Header className="justify-content-between py-0">
                    <span className='text-muted fw-medium'>
                        <FontAwesomeIcon type={'solid'} icon={'user-check'} iconClass={'me-1 text-success'}/>
                        Access Image
                    </span>
                    <div className='ms-5 d-flex justify-content-end'>
                        <Button variant='close' className="me-0 text-muted btn-active-primary" onClick={onHide}></Button>
                    </div>
                </Modal.Header>
                <Modal.Body className="p-0 d-flex border-0 flex-lg-row flex-column">
                    <div className="assets_modal_inner flex-column flex-grow-1 p-lg-4 p-xxl-5 position-relative mb-lg-0 overflow-auto">
                        <h3 className='text-center mb-4'>Your UGC Request Approved</h3>
                        <div className='mx-auto' style={{maxWidth:450}}>
                            <div className={'d-flex flex-column w-100'}>
                                <ImageType mediaClass={'position-relative overflow-hidden w-100'} size={100} height={64} width={64} url={ugcRequestData.postFile} alt={""} isCover={true} />
                                <div className='p-4'>
                                    <div className="d-flex align-items-start mb-2">
                                        <div className="d-flex align-items-start me-2 flex-grow-1 overflow-hidden text-decoration-none">
                                            <Author className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                imageSize={40}
                                                symbolClass={'symbol-label fw-bolder fs-5'}
                                                name={ugcRequestData.name} profileUrl={ugcRequestData.picture} networkId={ugcRequestData.networkId} />
                                            <div className="pe-2 w-100 overflow-hidden">
                                                <p className=" mb-0"><strong className="text-break text-ellipsis w-100 d-block">{ugcRequestData.name}</strong></p>
                                                <p className="mb-1 text-gray-500 text-break fs-9">@{ugcRequestData.username}</p>
                                            </div>
                                        </div>  
                                        <span className="d-flex align-items-center h-20px w-20px flex-shrink-0">
                                            <OverlayTrigger placement="top" overlay={<Tooltip>{GET_NETWORK_NAME(ugcRequestData.networkId)}</Tooltip>}>
                                                <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(ugcRequestData.networkId)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(ugcRequestData.networkId)} data-invert={GET_NETWORK_NAME(ugcRequestData.networkId)} />
                                            </OverlayTrigger>
                                        </span>
                                    </div>
                                    <ShowMoreContent classes={'mb-3'} content={ugcRequestData.content} idIndex={ugcRequestData.id} line={4} />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="full_modal_sidebar position-relative">
                        <div className="p-lg-6 p-4 py-3 rights_modal_aside_wrap rights_no_wizard">
                            <div className="ugc-information">
                                <h2 className="mb-5 h3">Information</h2>
                                <div className="form-group">
                                    <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom px-5 py-4">
                                        <span className="text-muted mr-2">User handle</span><span className=" m_grid_uername">{`@${ugcRequestData.username}`}</span>
                                    </div>
                                    <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom px-5 py-4">
                                        <span className="text-muted mr-2">User name</span><span className=" m_grid_name">{ugcRequestData.name}</span>
                                    </div>
                                    <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                        <span className="text-muted mr-2">Device</span>
                                        <span className=" ugc-deviceupdate">
                                            {ugcRequestData.userAgent ?
                                                HTMLReactParser(ugcRequestData.userAgent)
                                                : null
                                            }
                                        </span>
                                    </div>
                                    <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                        <span className="text-muted mr-2">IP Address</span><span className=" ugc-userIP"> {
                                            ugcRequestData.userIP
                                        }</span>
                                    </div>
                                    <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                        <span className="text-muted mr-2">Location</span><span className=" ugc-userLocation">
                                            {
                                                ugcRequestData.userLocation
                                            }
                                        </span>
                                    </div>
                                    <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom px-5 py-4">
                                        <span className="text-muted mr-2">Post Approval Date</span><span className=" ugc-actionTime">{moment(ugcRequestData.actionTime).utcOffset(0).format('YYYY-MM-DD  hh:mm:ss a')}</span>
                                    </div>
                                    <div className="d-flex justify-content-between flex-wrap align-items-center px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                        <span className="text-muted mr-2">TimeZone</span>
                                        <span className=" ugc-timezone">{ugcRequestData.timezone ? HTMLReactParser(ugcRequestData.timezone) : null}</span>
                                    </div>
                                    <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom px-5 py-4">
                                        <span className="text-muted mr-2">Terms &amp; Condition</span>
                                        <span className=" ugc-tncdownload">
                                            <a className='btn btn-link' href={(ugcRequestData.custom_terms) ? `${WASABI_PATH}test/${ugcRequestData.custom_terms}` : `${WASABI_PATH}test/${ugcRequestData.custom_term_type}`} target="_blank"><span className="ugc-terms overflow-hidden text-ellipsis text-nowrap" title={ugcRequestData.custom_term_type}>{ugcRequestData.custom_term_type}</span>
                                            <FontAwesomeIcon type={'solid'} icon={'download'} iconClass={'ms-1'}/></a>
                                        </span>
                                    </div>
                                    <div className="d-flex justify-content-between flex-wrap align-items-center border-top px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                        <span className="text-muted mb-2">Approval Certificate</span>
                                        <span className="f-between w-100">
                                            Rights Management 
                                            <a className='btn btn-dark ms-1' onClick={() => DOWNLOAD_CERTIFICATE(ugcRequestData.ugcid)} title='download'>Download
                                                <FontAwesomeIcon type={'solid'} icon={'download'} iconClass={'ms-1'}/>
                                            </a>
                                        </span>
                                    </div>
                                </div>
                            </div>
                            {ugcRequestData.comment ? <div className="ugc-user-comment mt-7">
                                <h2 className="mb-5">User Comment</h2>
                                <div className="user-comment-box p-5">
                                    <p>{ugcRequestData.comment}</p>
                                </div>
                            </div> : null}
                        </div>
                        <div className="d-flex justify-content-end p-4 border-top">
                            <Button variant="secondary" className="me-3 btn-min-w" onClick={onHide} >Close</Button>
                            <Button className="btn-min-w" onClick={() => this.onDownloadMedia(ugcRequestData)}>Download</Button>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>

        </> : null

    }
}

export default UgcModalApproved