import React, { Component } from 'react'
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import '@splidejs/splide/css';
import { Button, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../../utils';
import ShowMoreContent from '../../../CommonComponents/ShowMoreContent';
import ImageType from '../../../CommonComponents/MediaType/ImageType';
import Author from '../../../CommonComponents/Author/Author';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
//import { Options } from '@splidejs/splide';



export default class CardPreview extends Component {
  constructor(props) {
    super(props);
    this.mainRef = React.createRef();
  }

  handleThumbs = (id) => {
    if (this.mainRef.current) {
      this.mainRef.current.go(id);
    }
  };

  render() {

    const {ugcData} = this.props

    const mainOptions = {
      type      : 'loop',
      perPage   : 1,
      perMove   : 1,
      gap       : '1rem',
      pagination: false,
      height    : '10rem',
      navigation: false,
      
    };

    if(ugcData.data && ugcData.data.length == 1) mainOptions.arrows= false
    return <div className='mx-auto' style={{maxWidth:450}}>
              <Splide hasTrack={ false } options={{ arrows: false }}>
                <SplideTrack>
                {ugcData.data && ugcData.data.length ? ugcData.data.map((item,index)=>{ 
                  return <SplideSlide key={index}>
                      <div className={'d-flex flex-column w-100'}>
                        <ImageType mediaClass={'position-relative overflow-hidden w-100'} size={100} height={64} width={64} url={item.postFile} alt={""} isCover={true} />
                        <div className='p-3'>
                          <div className="d-flex align-items-start mb-2">
                              <div className="d-flex align-items-start me-2 flex-grow-1 overflow-hidden text-decoration-none">
                                  <Author className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                      imageSize={40}
                                      symbolClass={'symbol-label fw-bolder fs-5'}
                                      name={item.name} profileUrl={item.picture} networkId={item.networkId} />
                                  <div className="pe-2 w-100 overflow-hidden">
                                      <p className=" mb-0"><strong className="text-break text-ellipsis w-100 d-block">{item.name}</strong></p>
                                      <p className="mb-1 text-gray-500 text-break fs-9">@{item.username}</p>
                                  </div>
                              </div>  
                              <span className="d-flex align-items-center h-20px w-20px flex-shrink-0">
                                  <OverlayTrigger placement="top" overlay={<Tooltip>{GET_NETWORK_NAME(item.networkId)}</Tooltip>}>
                                      <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.networkId)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(item.networkId)} data-invert={GET_NETWORK_NAME(item.networkId)} />
                                  </OverlayTrigger>
                              </span>
                          </div>
                          <ShowMoreContent classes={'mb-3'} content={item.content} idIndex={item.id} line={4} />
                        </div>
                      </div>
                  </SplideSlide>
                    }):null}
                </SplideTrack>
                {ugcData.data && ugcData.data.length > 1 ? <div className="splide__arrows">
                  <Button variant='secondary' className="splide__arrow splide__arrow--prev start-0">
                        <FontAwesomeIcon type={'solid'} icon={'arrow-right'}/>
                    </Button>
                  <Button variant='secondary' className="splide__arrow splide__arrow--next end-0">
                      <FontAwesomeIcon type={'solid'} icon={'arrow-right'}/>
                  </Button>
                </div> : null}
              </Splide>
            </div>

  }
}
