import { Image, Modal } from "react-bootstrap";
import { baseUrl } from "../../../../../../Assets/AssetsHelper";
import { Link } from "react-router-dom";

const PermissionError = ({show, onHide}) => {
  return (
    <>
      <Modal show={show} centered onHide={onHide}>
        <Modal.Body className="text-center p-7">
            <Image className="my-7" src={baseUrl('media/images/error/warning.svg')} height={70} width={70} fluid/>
            <p className="text-center my-5 fs-6 mx-auto fw-bold text-muted" style={{maxWidth:400}}>
            You do not have access to UGC Rights as per your current plan. Please upgrade your plan to unlock this feature.
            </p>
            <Link className="btn-min-w mb-5 btn btn-lg btn-primary" size="lg" to={'/price'} title='Upgrade'>Upgrade Now</Link>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default PermissionError;
