import React, { Component } from 'react'
import { v4 as uuidv4 } from 'uuid';
import moment from 'moment'
import DateRanges from './dateRange'
class DateRange extends Component {
    state = {
        startDate: moment().endOf('month').format("DD-MM-YYYY"),
        endDate: moment().format("DD-MM-YYYY"),
        maxDate: moment().endOf('month').format("DD-MM-YYYY")
    }
    handleOnApply = (event, picker) => {
        this.setState({ startDate: picker.startDate.format("YYYY-MM-DD"), endDate: picker.endDate.format("YYYY-MM-DD") })
        this.props.onDateRangeFilter(picker.startDate.format("YYYY-MM-DD"), picker.endDate.format("YYYY-MM-DD"))
    }
    componentWillReceiveProps(nextProps) {
        const { moderation } = nextProps
        if (moderation && Object.keys(moderation.filter).length > 0 && moderation.filter.startDate != "") {
            this.setState({ startDate: moment(moderation.filter.startDate).format("DD-MM-YYYY"), endDate: moment(moderation.filter.endDate).format("DD-MM-YYYY") })
        }
        if (moderation.filterBody && moderation.filterBody.dateRanges && Object.keys(moderation.filterBody.dateRanges).length == 0 && this.state.startDate != moment().subtract(30, 'days').format("DD-MM-YYYY")) {
            this.setState({ startDate: moment().subtract(30, 'days').format("DD-MM-YYYY"), endDate: moment().format("DD-MM-YYYY") })
        }
    }
    render() {
        const { children } = this.props;
        const { startDate, endDate, maxDate } = this.state;
        return <DateRanges children={children} startDate={startDate} endDate={endDate} maxDate={maxDate} handleOnApply={this.handleOnApply} key={uuidv4()} />
    }
}

export default DateRange
