import React, { useEffect, useRef, useState } from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import ModalMedia from "./Elements/ModalMedia";
import { CAROUSAL_POST_PRODUCTS } from "../../../../../actions/moderationActions";
import {getImageSize} from 'react-image-size';

const CarousalPost = (props) => {

    const { updateCarousalPost, item, handleAllImageError } = props

    const arrowShow = item.imageList.length > 1;
    const splideRef = useRef(null);
    const  winWidth = window.innerWidth
    const  winHeight = window.innerHeight
    const [mediaSize, setmediaSize] = useState({ height: 400, width: 400 })
    const [movePost, setmovePost] = useState(0)

    const settings = {
        type: "slide",
        perPage: 1,
        pagination: true,
        arrows: arrowShow,
        start: 0,
    };

    useEffect(() => {
        const ids = item.imageList.map(item => item.referenceId).join(',');
        onMediaLoad(item.imageList[0])

        CAROUSAL_POST_PRODUCTS(ids).then(({ data: { responseCode, responseData } }) => {
            if (responseCode === 200) {
                setmovePost(item.imageList[0].referenceId)
                updateCarousalPost(item.imageList[0].referenceId, responseData.postproduct)
            } 
        }).catch((error) => {
            console.log("error", error)
            updateCarousalPost([])
        })
    }, [item]);


    const onMediaLoad = async (modalData) => {
        if (modalData.mediaHeight && modalData.mediaWidth) {
            setmediaSize({
                height: modalData.mediaHeight,
                width: modalData.mediaWidth
            })
        } else {
            try {
                const { width, height } = await getImageSize(modalData.newMedia);
                setmediaSize({
                    height: height,
                    width: width
                })
            } catch (error) {
                setmediaSize({
                    height: 100,
                    width: 100
                })
            }
        }
    }

    useEffect(() => {
        const splideInstance = splideRef.current?.splide;
        if (splideInstance) splideInstance.on("move", () => {
            onMediaLoad(item.imageList[splideInstance.index])
            updateCarousalPost(item.imageList[splideInstance.index].referenceId)
            setmovePost(item.imageList[splideInstance.index].referenceId)
        })

        return () => {
            splideInstance?.destroy();
        };
    }, [item.imageList]);

    return (
        <div className="m_c_p m-auto">
            {  item.imageList && item.imageList.length > 0 ? (
                <Splide
                    ref={splideRef}
                    hasTrack={false}
                    options={settings}
                    key={item.id}
                >
                    <SplideTrack>
                        {item.imageList.map((assetItem, assetKey) => (
                            <SplideSlide key={assetKey} className={'f-center'}>
                                <ModalMedia
                                    modalData={assetItem}
                                    movePost={movePost}
                                    handleAllImageError={handleAllImageError}
                                    autoPlay={assetItem.referenceId==movePost}
                                />
                            </SplideSlide>
                        ))}
                    </SplideTrack>
                </Splide>
            ) : null}
        </div>
    );
};

export default CarousalPost;
