import React from 'react'
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Button } from 'react-bootstrap';

const ModerationPagination = ({ moderation, onNextNavigateClick, onPrevNavigateClick , onChangePage}) => {
    const { paginateData } = moderation;
    if (!paginateData || !Object.keys(paginateData).length) return null;
    
    return <ul className="pagination pagination-sm my-1 ms-3">
            <li key={'li-prev'} className="page-item">
                <span className={`page-link ${paginateData.currentPage == 1 ? ' disabled' : ' cursor-pointer'}`} onClick={paginateData.currentPage == 1 ? null : onPrevNavigateClick}>
                    <FontAwesomeIcon type={'solid'} icon={'angle-left'} />
                </span>
            </li>
            {
            (paginateData.pages && paginateData.pages.length > 0) ? paginateData.pages.map((item, pagesIndex) => {

                return (parseInt(pagesIndex) < 1) ?
                    <li key={`li-${item}`} className={`page-item${paginateData.currentPage == item ? " active" : ' cursor-pointer'}`} onClick={onChangePage(item)}>
                        <span className="page-link">{item}</span>
                    </li> :

                    (((paginateData.currentPage <= 3) && (parseInt(pagesIndex) === 2 || parseInt(pagesIndex) === 1)) || (paginateData.currentPage >= parseInt(paginateData.pages.length) - 2 && (parseInt(pagesIndex) >= parseInt(paginateData.pages.length) - 3))) ?
                    <React.Fragment key={`fragment-${pagesIndex}`}>
                        {pagesIndex === parseInt(paginateData.pages.length) - 3 && parseInt(paginateData.pages.length) > 4 ?
                            <li key={`li-2-${pagesIndex}`} className="page-item disabled">
                                <span className="page-link">...</span>
                            </li>:
                            null
                        }
                        <li key={`li-3-${pagesIndex}`} className={`page-item${paginateData.currentPage == item ? " active" : ' cursor-pointer'}`} onClick={onChangePage(item)}>
                            <span className="page-link">{item}</span>
                        </li>
                    </React.Fragment>
                    :
                    (parseInt(paginateData.pages.length) - 3 == pagesIndex) ?
                    (paginateData.currentPage > 3 && paginateData.currentPage < paginateData.pages.length - 2) ?
                        <React.Fragment key={`fragment-${pagesIndex}`}>
                            <li key={`li-4-${pagesIndex}`} className="page-item disabled">
                                <span className="page-link">...</span>
                            </li>
                            <li key={`li-5-${pagesIndex}`} className={`page-item active`} onClick={onChangePage(item)}>
                                <span className="page-link">{paginateData.currentPage}</span>
                            </li>
                            <li key={`li-6-${pagesIndex}`} className="page-item disabled">
                                <span className="page-link">...</span>
                            </li>
                        </React.Fragment>
                        :
                        <li key={`li-7-${pagesIndex}`} className="page-item disabled"> <span className="page-link">...</span> </li>

                    : (parseInt(paginateData.pages.length) - 1 == pagesIndex) ?
                    <li key={`li-8-${pagesIndex}`} className={`page-item ${paginateData.currentPage == item ? " active" : ' cursor-pointer'}`} onClick={onChangePage(item)}>
                        <span className="page-link">{item}</span>
                    </li>
                    : null
            }) : null}
            <li key={'li-next'} className="page-item">
                <span className={`page-link ${paginateData.currentPage == paginateData.totalPages ? ' disabled' : ' cursor-pointer'}`} onClick={paginateData.currentPage == paginateData.totalPages ? null : onNextNavigateClick}>
                    <FontAwesomeIcon type={'solid'} icon={'angle-right'} />
                </span>
            </li>
        </ul>
}
export default ModerationPagination;
