import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import Select from 'react-select'
import { GET_UGC_POST_DETAIL, ugcModal } from '../../../../../../actions/moderationActions'
import { Container, Tooltip, OverlayTrigger, Badge, Button, Form } from 'react-bootstrap'
import ModerationPagination from '../../Elements/Pagination'
import ErrorModal from '../../UgcRights/Elements/ErrorModal'
import PermissionError from '../../UgcRights/Elements/PermissionError'
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper'
import { checkPermission } from '../../../../../../actions/collaboratorActions'


const ModSubHeader = (props) => {
    const [addStatus, setAddTag] = useState(false)
    const categories = [{ label: "50", value: 50 }, { label: "100", value: 100 }, { label: "200", value: 200 }]
    const [selectedEntry, selectedEntryFun] = useState([{ label: "50", value: 50 }])
    const [permissionError, setPermissionError] = useState(false)
    const [ugcError, setUgcError] = useState(false)
    const [showGetRight, setShowGetRight] = useState(false)
    const [fromSnapup, setFromSnapup] = useState(false)
    const { moderation, activeTab } = props
    const dispatch = useDispatch()
    const updateLimit = event => { 
        props.limitUpdate(parseInt(event.value))
        selectedEntryFun(event) 
    }
    const refreshPost = () => { props.clickToRefresh(); } 


    useEffect(() => {
        return () => {
            checkGetRight()
        };
    }, [moderation])


    const getRights = () => {
        const { check_uncheck_all } = moderation
        let userId = null
        var flag = false
        check_uncheck_all.data.map(post => {
            if (post.ugcstatus == 1 && !flag) {
                setPermissionError(true)
                flag = true
            }
            if (post.networkId == 29) {
                setPermissionError(true)
                setFromSnapup(true)
                flag = true
            }
            if (post.type == 1) {
                setUgcError(true)
                flag = true
            }
            if (!((post.networkId == 3 && post.feedFilter == 62) || (post.networkId == 18 && (post.feedFilter == 24 || post.feedFilter == 25 || post.feedFilter == 26)))) {
                setPermissionError(true)
                setFromSnapup(true)
                flag = true
            }

            if (userId == null) userId = post.username
            else if (userId != null && userId != post.username && !flag) {
                setUgcError(true)
                flag = true
            }
        })
        if (!flag) {
            const id = check_uncheck_all.data.map(item => item.id)
            dispatch(GET_UGC_POST_DETAIL({ is_carausel: 0, postId: id, id, }))
            dispatch(ugcModal(true))
        }
    }

    const checkGetRight = () => {
        const { check_uncheck_all } = moderation
        var flag = false
        check_uncheck_all.data.map(post => {
            if (post.ugcstatus == 1 && !flag) flag = true
            if (post.networkId == 29) flag = true
            if (post.type == 1) flag = true
            if (!((post.networkId == 3 && post.feedFilter == 62) || (post.networkId == 18 && (post.feedFilter == 24 || post.feedFilter == 25 || post.feedFilter == 26)))) flag = true
        })
        setShowGetRight(!flag)
    }

    return <Container fluid>
            <div className="f-between py-1 mod_header">
                <div className="d-flex align-items-center">
                    <Form.Check type="checkbox" id="select_all_" className='btn btn-secondary btn-sm px-2 me-2 text-nowrap my-1' label={`${moderation.check_uncheck_all.data.length ? moderation.check_uncheck_all.data.length +` Selected` : `Select All`}`} onChange={props.clickToCheckAll(moderation.check_uncheck_all.status)} checked={moderation.check_uncheck_all.status}/>
                    <OverlayTrigger placement="top" overlay={<Tooltip>Refresh</Tooltip>}>
                        <Button variant='icon' size='sm' className="btn-active-primary" aria-label='refresh' onClick={() => refreshPost()}>
                            <span className='d-inline-block'><FontAwesomeIcon icon={'arrows-rotate'} /></span>
                        </Button>
                    </OverlayTrigger>
                    {moderation.check_uncheck_all && moderation.check_uncheck_all.data.length > 0 ? 
                    <div className="d-flex align-items-center mx-lg-2 mod_actions flex-wrap">
                        {activeTab == 0 || activeTab == 2 ? <Button variant='success' size="sm" className="me-2 my-1" onClick={props.onClickToFilterUpdateStatus(1)}>
                            <FontAwesomeIcon icon={'check'} iconClass='me-1'/>
                            Public
                        </Button>: null}
                        {activeTab == 0 || activeTab == 1 ? <Button variant='danger' size="sm" className="me-2 my-1" onClick={props.onClickToFilterUpdateStatus(2)}>
                            <FontAwesomeIcon icon={'xmark'} iconClass='me-1'/>
                            Private
                        </Button>: null}
                        <Button variant='warning' size="sm" className="me-2 my-1" onClick={props.onClickToFilterDeleteAll}>
                            <FontAwesomeIcon icon={'trash-can'} iconClass='me-1'/>
                            Delete
                        </Button>
                        {/* {!(UserRuleManualTag || UserRuleEnableAI) ? <Button variant='primary' size="sm" className="me-2" onClick={() => addTag()}>
                            <FontAwesomeIcon type="solid" icon={'tag'} iconClass='me-1'/>
                            Add Tag
                        </Button>:null} */}
                        {showGetRight ? <Button variant='dark' size="sm" className="me-2 my-1" onClick={() => checkPermission('contentGallery', 'editGallery', true) ? getRights() : {}}>
                            <FontAwesomeIcon icon={'user-check'} iconClass='me-1'/>
                            Get Rights
                        </Button>:null}
                    </div>
                    
                    :null}
                </div>
                <div className="d-flex align-items-center">
                    <Select className={`selectpicker selectpicker-sm d-none d-lg-flex`} classNamePrefix="selectpicker" isSearchable={false} options={categories} value={selectedEntry} onChange={e => updateLimit(e)} /> 
                    <ModerationPagination {...props} />
                </div>
            </div>
        <PermissionError show={permissionError} fromSnapup={fromSnapup} onHide={() => setPermissionError(false)} />
        <ErrorModal show={ugcError} onHide={() => setUgcError(false)} />
    </Container>

}
export default ModSubHeader;