import { Component } from "react";
import { Modal, Button, InputGroup, Form, Image, OverlayTrigger, Tooltip } from "react-bootstrap";
import { DashboardModal, Dashboard } from "@uppy/react";
import { FontAwesomeIcon } from "../../../../../../Assets/AssetsHelper";
import $ from "jquery";
import { ADD_UGC_TEMPLATE } from "../../../../../../actions/moderationActions";
import { connect } from "react-redux";
import { UGC_UPLOAD_URL, WASABI_PATH } from "../../../../../../actions/api";
import UppyUpload from "../../../../../../utils/uppyUpload";
import { TEMP_URL } from "../../../../../../actions/constants";



class CreateTemplate extends Component {
  uppyUploader = new UppyUpload()
  uppyUploaderPdf = new UppyUpload(['application/pdf', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'])
  state = { postauthor: null, posthandle: null, authorpicture: null, postcarousel: false, content: null, data: null, modalOpen: false, createTemplate: false, brandFilename: this.props.editData.client_logo, selectedBrand: this.props.editData.client_logo_name, selectedBrandFile: this.props.editData.client_logo, tncFilename: this.props.editData.custom_terms, selectedtnc: this.props.editData.custom_terms_name, selectedtncFile: this.props.editData.custom_terms, selectedTemplate: null, acceptTnc: false, logoError: false, tncError: false, templateName: this.props.editData.name, custom_msg: this.props.editData.custom_message, tncerrorMsg: false, nameerrorMsg: false, msgerrorMsg: false, };

  componentDidMount() {
    this.uppyUploader.uppy.on("transloadit:result", (stepName, result) => {
      this.setState(
        {
          selectedBrandFile: `${TEMP_URL}${result.id}.${result.ext}`,
          brandFilename: result.name,
          selectedBrand: `${TEMP_URL}${result.id}.${result.ext}`,
          logoError: false
        },
        () => {
          setTimeout(() => {
            this.uppyUploader.uppy.cancelAll();
            this.setState({ brandModalOpen: false });
          }, 300);
        }
      );
    });

    this.uppyUploaderPdf.uppy.on("transloadit:result", (stepName, result) => {
      this.setState(
        {
          selectedtncFile: `${TEMP_URL}${result.id}.${result.ext}`,
          tncFilename: result.name,
          selectedtnc: `${TEMP_URL}${result.id}.${result.ext}`,
          tncerrorMsg: false
        },
        () => {
          setTimeout(() => {
            this.uppyUploaderPdf.uppy.cancelAll();
            this.setState({ tncModalOpen: false });
          }, 300);
        }
      );
    });

    // this.props.ugcData
  }


  onClickToOpenBrand = () => {
    this.setState({ brandModalOpen: true });
    $(".uppy-Dashboard-close").click(() => {
      this.uppyUploader.uppy.cancelAll();
      this.setState({ brandModalOpen: false });
    });
  };
  onClickToCloseBrand = () => {
    this.uppyUploader.uppy.cancelAll();
    this.setState({ brandModalOpen: false });
  };

  onClickToOpenTnc = () => {
    this.setState({ tncModalOpen: true });
    $(".uppy-Dashboard-close").click(() => {
      this.uppyUploaderPdf.uppy.cancelAll();
      this.setState({ tncModalOpen: false });
    });
  };
  onClickToCloseTnc = () => {
    this.uppyUploaderPdf.uppy.cancelAll();
    this.setState({ tncModalOpen: false });
  };

  removeBrandLogo = () => {
    this.setState({ selectedBrand: "", selectedBrandFile: null, brandFilename: null })
  }
  removeTnc = () => {
    this.setState({
      tncFilename: null,
      selectedtnc: "",
      selectedtncFile: null,
    })
  }

  onSaveTemplate = () => {
    const { createTemplate, selectedBrand, tncFilename, brandFilename, selectedtnc, selectedtncFile, selectedTemplate, templateName, selectedBrandFile, custom_msg } = this.state;

    if (!custom_msg || !templateName || !tncFilename || !brandFilename) {
      if (!custom_msg) this.setState({ msgerrorMsg: true })
      if (!templateName) this.setState({ nameerrorMsg: true })
      if (!tncFilename) this.setState({ tncerrorMsg: true })
      if (!brandFilename) this.setState({ logoError: true })
    }
    else {
      var obj = { owner: localStorage.getItem('ownerId'), name: templateName, custom_message: custom_msg, custom_terms: selectedtnc, custom_terms_name: tncFilename, client_logo: selectedBrand, client_logo_name: brandFilename }
      if (this.props.edit) obj = { ...obj, id: this.props.editData.id }
      this.props.ADD_UGC_TEMPLATE(obj, this.props.onAddNewTemplate)
    }
  }



  render() {
    const { show, onHide, saveTemplate, edit } = this.props;
    const { brandModalOpen, tncModalOpen, createTemplate, selectedBrand, tncFilename, logoError, selectedtncFile, selectedTemplate, acceptTnc, tncError, selectedBrandFile, templateName, custom_msg, brandFilename, tncerrorMsg, nameerrorMsg, msgerrorMsg } = this.state;


    return (
      <>
        <Modal size="lg" show={show} onHide={onHide} centered>
          <Modal.Header closeButton>
            <Modal.Title>{edit ? 'Edit' : `Create New`} Template</Modal.Title>
          </Modal.Header>
          <Modal.Body>
                  <Form.Group className="mb-3">
                    <Form.Label htmlFor="t_name">Template Name <span className="text-danger">*</span> </Form.Label>
                    <Form.Control placeholder="enter your template name" autoComplete="off" id="t_name" className={`${nameerrorMsg ? `is-invalid` : ``}`} value={templateName} onChange={(e) => this.setState({ templateName: e.target.value, nameerrorMsg: false })} maxLength="255" />
                    <span id="t_nameerror" className="invalid-feedback" >{nameerrorMsg ? "Name is required" : ''}</span>
                  </Form.Group>
                  <Form.Group className="mb-3">
                    <Form.Label htmlFor="t_msg">Custom Message <span className="text-danger">*</span> </Form.Label>
                    <Form.Control as={'textarea'} className={`${msgerrorMsg ? `is-invalid` : ``}`} id="t_msg" maxLength="255" cols="30" rows="6" value={custom_msg} onChange={(e) => this.setState({ custom_msg: e.target.value, msgerrorMsg: false })} />
                    <span id="t_msgerror" className="invalid-feedback" >{msgerrorMsg ? "Custom message is required" : ''}</span>
                  </Form.Group>

                  <Form.Group className="mb-3">
                    <Form.Label htmlFor="t_logo">Brand Logo <span className="text-danger">*</span></Form.Label>
                    {!selectedBrandFile ? <div className={`p-6 border border-dashed rounded-2 cursor-pointer ${logoError ? `is-invalid border-danger` : ``}`} onClick={() => this.onClickToOpenBrand()} style={{maxWidth:300}}>
                      <p className="mb-0 text-center text-muted fw-medium">Upload Logo</p>
                    </div> :
                    <div className="d-flex align-items-center">
                        <div className="me-3 me-xxl-4">
                            <div className="symbol h-90px w-90px position-relative rounded-2 overflow-hidden">
                                <Image src={selectedBrandFile?.includes('https://cloud.tagshop.ai/') ? selectedBrandFile : `${WASABI_PATH}${UGC_UPLOAD_URL}/${selectedBrandFile}`} alt={''} height={90} width={90} className="object-center-cover" load="lazy" fluid />
                            </div>
                        </div>
                        <div className="d-flex align-items-center flex-wrap">
                            <div className="d-flex flex-column align-items-center flex-nowrap mb-0">
                                <OverlayTrigger placement="top" overlay={<Tooltip>Remove</Tooltip>}>
                                    <Button variant="icon" className="btn-active-danger" aria-label="remove" onClick={this.removeBrandLogo}>
                                        <FontAwesomeIcon type={'solid'} icon={'trash-can'}/>
                                    </Button>
                                </OverlayTrigger>
                            </div>
                        </div>
                    </div>
                    }
                    <span id="t_logoerror" className="invalid-feedback">Brand Logo is required</span>
                  </Form.Group>

                  <Form.Group className="mb-3">
                    <Form.Label htmlFor="t_msg">Terms and Conditions<span className="text-danger">*</span></Form.Label>
                    <InputGroup className={`${tncerrorMsg ? `is-invalid` : ``}`}>
                      <div className="form-control form-control-lg">
                        {selectedtncFile ? 
                        <div className="overflow-hidden d-flex align-items-center"> 
                        <span className="text-ellipsis fs-8 p-1 rounded-1">{tncFilename}</span> 
                        <span className="cursor-pointer p-1 mx-1 rounded-1 f-center text-center text-danger" onClick={this.removeTnc}>
                          <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                        </span>
                      </div> : ''}
                      </div>
                      <InputGroup.Text className="cursor-pointer" onClick={() => this.onClickToOpenTnc()}>Attach File</InputGroup.Text>
                    </InputGroup>
                    <span id="tcError" className="invalid-feedback" >{tncerrorMsg ? "Terms & Conditions is required" : ''}</span>
                  </Form.Group>
          </Modal.Body>
          <Modal.Footer>
                <Button variant='secondary' className='ms-3 btn-min-w' onClick={onHide}>Cancel</Button>
                <Button className='ms-3 btn-min-w' onClick={this.onSaveTemplate}>Save Template</Button>
            </Modal.Footer>
          <DashboardModal
            animateOpenClose={true}
            proudlyDisplayPoweredByUppy={false}
            inline={false}
            showProgressDetails={true}
            height={750}
            browserBackButtonClose={true}
            showRemoveButtonAfterComplete={false}
            uppy={this.uppyUploader.uppy}
            open={brandModalOpen}
            onRequestCloseModal={this.onClickToCloseBrand}
            closeAfterFinish={true}
            doneButtonHandler={this.doneButtonHandler}
            plugins={["Instagram", "GoogleDrive", "Url", "Webcam"]}
          />
          <DashboardModal
            animateOpenClose={true}
            proudlyDisplayPoweredByUppy={false}
            inline={false}
            showProgressDetails={true}
            height={750}
            browserBackButtonClose={true}
            showRemoveButtonAfterComplete={false}
            uppy={this.uppyUploaderPdf.uppy}
            open={tncModalOpen}
            onRequestCloseModal={this.onClickToCloseTnc}
            closeAfterFinish={true}
            doneButtonHandler={this.doneButtonHandler}
            plugins={["Url", "Webcam"]}
          />

        </Modal>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    // ugcData: state.moderation.ugcData,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    ADD_UGC_TEMPLATE: (data, callback) => dispatch(ADD_UGC_TEMPLATE(data, callback))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateTemplate);
