import React, { Component } from 'react'
import { connect } from "react-redux";
import parse from 'react-html-parser';
import moment from 'moment';
import { clickToPostPin, clickToDeletePost, clickToBlockUser, clickToPostStatus, GET_UGC_POST_DETAIL, ugcModal, GET_UGC_REQUEST, UPDATE_INSTA_POST_ACTION } from '../../../../actions/moderationActions'
import ProductTags from './Elements/TagedProducts'
import Tags from './Elements/Tags'
import { TIKTOK_UPDATE_STATUS, GET_NETWORK_ICON, GET_NETWORK_NAME, isRefresh } from '../../../../utils';
import ChangeOrderModal from './Elements/ChangeOrderModal'
import ErrorFound from '../../../ErrorFound';
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import { OverlayTrigger, Dropdown, Tooltip, Card, Form, Button, Image } from 'react-bootstrap'
import { baseUrl, ImagePlaceholder } from '../../../../Assets/AssetsHelper';
import ContentModal from './ContentModal/ContentModal';
import Masonry from "react-masonry-component";
import Author from '../CommonComponents/Author/Author';
import TextType from '../CommonComponents/MediaType/TextType';
import ImageType from '../CommonComponents/MediaType/ImageType';
import VideoType from '../CommonComponents/MediaType/VideoType';
import RefreshPostData from './Elements/RefreshPost'
import Rating from '../CommonComponents/Rating';
import { checkPermission } from '../../../../actions/collaboratorActions';
import { Link } from 'react-router-dom';



class MasonryView extends Component {
    imageQueue = [];
    state = { postID: null, id: null, data: null, itemIndex: null, modalData: null, firstRequest: false, openpinToTopModal: false, showModal: false, currentIndex: null }
    tourImport = null


    clickToPin = (index, postId, status) => event => { if (checkPermission('contentGallery', 'editGallery', true)) this.props.clickToPostPin(index, postId, status) }

    clickToGetUgc = (item) => event => { if (checkPermission('contentGallery', 'editGallery', true)) { this.props.getUgcDetails(item); this.openUgcModal() } }

    clickToDeletePost = (item, index) => event => { if (checkPermission('contentGallery', 'editGallery', true)) this.props.clickToDeletePost(item, index, this.props.navigate) }

    clickToBlockUser = (index, userName) => event => { if (checkPermission('contentGallery', 'editGallery', true)) this.props.clickToBlockUser(index, userName) }

    clickToPostStatus = (index, postId, value) => event => { if (checkPermission('contentGallery', 'editGallery', true)) this.props.clickToPostStatus(index, postId, value) }

    onClickToPostTags = (tags) => event => { this.props.searchTagsFilter(tags) }

    openUgcModal = (data) => { this.props.ugcModal(true) }

    clickToGetLink = (type, item) => event => {
        this.props.getUgcDetails(item)
        this.props.GET_UGC_REQUEST(item)
        this.props.onShow(type, item.ugcmodified)
    }

    handleImageError = (e) => {
        const src = e.target.getAttribute("data-id");
        if (!this.imageQueue.includes(src)) {
            this.imageQueue.push(src);
            if (!this.state.firstRequest) this.processImageQueue();
        }
    };

    processImageQueue = async () => {
        this.setState({ firstRequest: true })
        const src = this.imageQueue[0];
        const imgTarget = document.getElementById(`imgID${src}`);
        if (src) {
            TIKTOK_UPDATE_STATUS(src).then(({ data }) => {
                this.imageQueue.shift();
                if (imgTarget) imgTarget.src = data.media;
                this.processImageQueue()
            })
                .catch((error) => {
                    this.imageQueue.shift();
                    if (imgTarget) imgTarget.src = ImagePlaceholder;
                    this.processImageQueue()
                })
        }
    };

    onRefreshPost = async (item) => {
        const { UPDATE_INSTA_POST_ACTION } = this.props
        if (checkPermission('contentGallery', 'editGallery', true)) await UPDATE_INSTA_POST_ACTION(item)

    }

    handleModalShow = (data, index) => {
        this.setState({ showModal: true, modalData: data, currentIndex: index })
    }

    handleCallback = (dataEvent) => {
        this.setState({
            showModal: dataEvent, modalData: dataEvent ? this.state.modalData : null, currentIndex: null
        })
    }
    componentDidUpdate(prevProps) {
        if (this.props.listData !== prevProps.listData) {
            this.setState({ modalData: this.props.listData[this.state.currentIndex] });
        }
    }


    slideAssets = (currentIndex) => event => {
        const { listData } = this.props
        if (currentIndex === -1) {
            let count = listData.length - 1
            this.setState({ currentIndex: count, modalData: listData[count] });
        }
        else {
            let count = listData.length == currentIndex ? 0 : currentIndex
            this.setState({ currentIndex: count, modalData: listData[count] });
        }
    };

    componentDidMount() {
        const { moderation } = this.props;
        const tourStep = localStorage.getItem('tourStep');
        if (localStorage.getItem('isTour') === 'true' && moderation?.listData?.length && (tourStep === '4' || tourStep === '5')) {
            this.checkTour()

        }
    }

    checkTour = async () => {
        const tourStep = localStorage.getItem('tourStep');
        if (!this.tourImport) this.tourImport = import('../../../../actions/appTour')
        const { default: appTour } = await this.tourImport
        const { tour } = new appTour(this.props.navigate);
        if (tourStep === '4') setTimeout(() => { this.props.navigate('/content/products'); tour.drive(2); }, 1000);
        else if (tourStep === '5') setTimeout(() => tour.drive(3), 1000);
    };

    render() {
        const PersonalizationUgcRights = 1
        const { total, activeTab, filter, moderation, getUgcDetails, ugcData, ugcPopup, ugcRequestData, onShow, postCarosal, handleAllImageError } = this.props
        const { openpinToTopModal, showModal, modalData } = this.state;
        let selection = filter?.tags.join()

        return (<div className="w-100 mb-4 mb-lg-0">
            <Masonry className={`row gx-2`} elementType={"div"} disableImagesLoaded={false} updateOnEachImageLoad={true} enableResizableChildren > {
                (moderation?.listData && moderation?.listData.length > 0) ? moderation?.listData.map((item, index) => {
                    return (item.status == activeTab || activeTab == 0) ?
                        <article key={`post${index}_${item.id}`} aria-labelledby={item.postcontent} total-items={total} className={`assetsItem`} id={`content_id-${item.id}`} data-post-id={item.id} data-highlight={item.highlight} data-pinned={item.pin} data-created={item.createdAt}>
                            <ErrorFound>
                                <Card className='border-0'>
                                    <section className="f-between p-2" style={{ boxShadow: '0px 1px 0px rgba(0,0,0,.1)' }}>
                                        <div className="d-flex align-items-center list-inline mb-0" id={`post-actions-${item.id}`}>
                                            <Form.Check className='me-2' aria-label="select" checked={item.isChecked} value={item.isChecked} onChange={this.props.clickPostChecked(index)} />
                                            <OverlayTrigger placement="top" overlay={<Tooltip>Pin to Top</Tooltip>}>
                                                <span className={`w-18px f-center fs-8 me-2 cursor-pointer ${item.pin ? 'text-primary' : 'text-muted'}`} onClick={this.clickToPin(index, item.imageList?.length ? item.ptPost : item.postId, item.pin)} data-value={item.pin}>
                                                    <FontAwesomeIcon type={'solid'} icon={'thumbtack'} />
                                                </span>
                                            </OverlayTrigger>
                                            {(((item.networkId == 3 && item.feedFilter == 62) || (item.networkId == 18 && (item.feedFilter == 24 || item.feedFilter == 25 || item.feedFilter == 26))) && item.ugcstatus == null && item.type != 1 && localStorage.getItem('wallLimitExceed') != 'true') ?
                                                <OverlayTrigger placement="top" overlay={<Tooltip>Get Rights</Tooltip>}>
                                                    <span className="w-18px f-center fs-8 me-2 cursor-pointer" onClick={() => { if (checkPermission('contentGallery', 'editGallery', true)) { getUgcDetails(item); this.openUgcModal() } }}>
                                                        {item.ugcRights == null || item.ugcstatus == 4 ?
                                                            <FontAwesomeIcon type={'solid'} icon={'user-check'} iconClass={'text-muted'} />
                                                            : item.ugcstatus == 2 ?
                                                                <FontAwesomeIcon type={'solid'} icon={'user-check'} iconClass={'text-success'} />
                                                                : item.ugcstatus == 1 ?
                                                                    <FontAwesomeIcon type={'solid'} icon={'user-clock'} iconClass={'text-warning'} />
                                                                    : item.ugcstatus == 3 ?
                                                                        <FontAwesomeIcon type={'solid'} icon={'user-xmark'} iconClass={'text-danger'} />
                                                                        : ''}
                                                    </span>
                                                </OverlayTrigger>
                                                : null}

                                            {[18, 2].includes(item?.networkId) ?
                                                <RefreshPostData onRefreshPost={this.onRefreshPost} item={item} />
                                                : null}
                                        </div>

                                        <Dropdown>
                                            <OverlayTrigger placement="top" overlay={<Tooltip>More Actions</Tooltip>}>
                                                <Dropdown.Toggle variant='secondary' aria-label='more' className="arrow_disabled dropdown-toggle border-0 p-0">
                                                    <FontAwesomeIcon icon={'ellipsis'} />
                                                </Dropdown.Toggle>
                                            </OverlayTrigger>
                                            <Dropdown.Menu>
                                                {item.ugcstatus != 1 || item.ugcstatus != 2 ?
                                                    <Dropdown.Item onClick={this.clickToDeletePost(item, index)}>
                                                        <span className='me-2'><FontAwesomeIcon type={'solid'} icon={'trash-can'} /></span>
                                                        Delete Post
                                                    </Dropdown.Item> : null}
                                                <Dropdown.Item onClick={this.clickToBlockUser(index, item.username)} disabled={localStorage.getItem('wallLimitExceed') == 'true' ? true : false}>
                                                    <span className='me-2'><FontAwesomeIcon type={'solid'} icon={'ban'} /></span>
                                                    Block User
                                                </Dropdown.Item>
                                                {item.link != '' && item.link != null ?
                                                    <Dropdown.Item href={item.link} target="_blank">
                                                        <span className='me-2'><FontAwesomeIcon type={'solid'} icon={'paper-plane'} /></span>
                                                        View Post
                                                    </Dropdown.Item>
                                                    : null}
                                                {item.pin ?
                                                    <Dropdown.Item onClick={() => this.setState({ openpinToTopModal: true })}>
                                                        <span className='me-2'><FontAwesomeIcon type={'solid'} icon={'right-left'} /></span>
                                                        Change Order
                                                    </Dropdown.Item>
                                                    : ''}
                                            </Dropdown.Menu>
                                        </Dropdown>
                                    </section>
                                    <section className="cursor-pointer position-relative">
                                        {Object.keys(item?.recommendation || {}).length > 0 ?
                                            <div className={`image_type position-absolute start-0 rounded-4 f-center mt-2 ms-2 block-center recomm_type_${item.recommendation.type}`}>
                                                <div className={`h-10px w-10px rounded-circle d-flex flex-shrink-0 ${item.recommendation.type == 1 ? 'bg-success' : item.recommendation.type == 2 ? 'bg-warning' : item.recommendation.type == 3 ? 'bg-danger' : null}`}></div>
                                                <div className="img_quality_status"><span className="fs-10 text-ellipsis px-1 text-light fw-medium d-block">{item.recommendation.label}</span></div>
                                            </div>
                                            : null}
                                        <div onClick={() => localStorage.getItem('wallLimitExceed') != 'true' ? this.handleModalShow(item, index) : {}}>
                                            {item.imageList && item.imageList.length ?
                                                <span className='position-absolute top-0 end-0 p-2' style={{ zIndex: 2 }}>
                                                    <FontAwesomeIcon type={'solid'} icon={'clone'} iconClass={'text-white filter-shadow'} />
                                                </span> : null}

                                            {item.type == 1 ?
                                                <TextType mediaClass={'bg-light'} content={item.postcontent} rating={item.rating} socialNetworkId={item.networkId} />
                                                :
                                                (item.type == 2 || item.type == 4) ?
                                                    <ImageType mediaClass={'content_img_'} size={false} url={item.postFile} height={item.mediaHeight} width={item.mediaWidth} icon={item.networkId} isCover={true} rating={item.rating} alt={''} handleAllImageError={handleAllImageError} item={item} />
                                                    :
                                                    (item.type == 3 || item.type == 5) ?
                                                        <>{item.mediaClip ?
                                                            <VideoType item={item} mediaClass={'content_video'} size={false} url={item.mediaClip} poster={item.postFile} height={item.mediaHeight} width={item.mediaWidth} autoPlay={true} muted={true} isCover={true} controls={false} loop={true} />
                                                            : <ImageType mediaClass={'content_img_'} size={false} url={item.postFile} height={item.mediaHeight} width={item.mediaWidth} icon={item.networkId} rating={item.rating} alt={''} handleAllImageError={handleAllImageError} isCover={true} item={item} />}
                                                            <div className='position-absolute f-center h-100 w-100 start-0 top-0 text-white' style={{ fontSize: 45 }}>
                                                                <FontAwesomeIcon icon={'circle-play'} iconClass={'filter-shadow'} />
                                                            </div>
                                                        </>
                                                        :
                                                        null}
                                        </div>
                                        <div className="position-absolute w-100 start-0 bottom-0 p-3 w-100" style={{ overflowX: 'auto', zIndex: 1 }}>
                                            <ErrorFound><ProductTags item={item} openContent={(item, index) => this.handleModalShow(item, index)} index={index} navigate={this.props.navigate} /></ErrorFound>
                                        </div>
                                    </section>

                                    {(PersonalizationUgcRights == 1) ? (item.ugcstatus == 1 || item.ugcstatus == 2 || item.ugcstatus == 3 || item.ugcstatus == 4) ?
                                        <section className="w-100">
                                            {item.ugcstatus == 1 ?
                                                <div onClick={this.clickToGetLink(1, item)} className="bg-warning f-center text-white fw-medium p-1 w-100 fs-9">
                                                    <span className='me-1'>
                                                        <FontAwesomeIcon type={'solid'} icon={'user-clock'} />
                                                    </span>
                                                    Pending
                                                    {item.ugccreated ?
                                                        <small>
                                                            ({item.ugccreated ? moment(moment(item.ugcmodified, 'YYYY-MM-DD  HH:mm:ss').utcOffset(0)).fromNow() : null})
                                                        </small>
                                                        : null}
                                                </div>
                                                : null}
                                            {item.ugcstatus == 2 ?
                                                <div onClick={this.clickToGetLink(2, item)} className="bg-success f-center text-white fw-medium p-1 w-100 fs-9">
                                                    <span className='me-1'>
                                                        <FontAwesomeIcon type={'solid'} icon={'user-check'} />
                                                    </span>
                                                    Approved
                                                </div>
                                                : null}
                                            {item.ugcstatus == 3 ?
                                                <div onClick={this.clickToGetLink(3, item)} className="bg-danger f-center text-white fw-medium p-1 w-100 fs-9">
                                                    <span className='me-1'>
                                                        <FontAwesomeIcon type={'solid'} icon={'user-xmark'} />
                                                    </span>
                                                    Rejected
                                                </div>
                                                : null}

                                            {item.ugcstatus == 4 ?
                                                <div onClick={this.clickToGetLink(4, item)} className="bg-danger f-center text-white fw-medium p-1 w-100 fs-9">
                                                    <span className='me-1'>
                                                        <FontAwesomeIcon type={'solid'} icon={'user-xmark'} />
                                                    </span>
                                                    Expired
                                                </div>
                                                : null}
                                        </section>
                                        : null
                                        : null}

                                    {item.ctastatus == 1 && item.buttonText != '' ?
                                        <section className={`d-flex p-2 w-100 grid-cta-status-${item.id}`}>
                                            <a className="btn font-weight-bold text-nowrap w-100" title={item.buttonText} target="_blank" href={`${item.buttonUrl && item.buttonUrl.includes('http') ? item.buttonUrl : `//${item.buttonUrl}`}`} style={{ backgroundColor: item.ctaButtonColor, color: item.ctaTextColor }}>
                                                <span>{item.buttonText}</span>
                                            </a>
                                        </section>
                                        : null}
                                    {item.rating > 0 && item.type != 1 ? <section className='d-flex justify-content-start p-3 pb-0'>
                                        <Rating className={'fs-5'} iconClass="me-1" rating={item.rating} />
                                    </section> : null}
                                    <section className="p-3">
                                        <Tags item={item} onClickToPostTags={this.onClickToPostTags} selection={selection} key={item.tags} />
                                        <div className="f-between">
                                            <div className="d-flex align-items-center pe-2" style={{ maxWidth: 'calc(100% - 25px)' }}>
                                                <Author imageSize={36}
                                                    symbolClass={'symbol-label fs-5'}
                                                    className={'symbol symbol-36 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                    name={parse(item.username)} profileUrl={item.picture} networkId={item.networkId} />
                                                <div className="d-flex flex-column flex-grow-1 overflow-hidden">
                                                    {item?.link ?
                                                        <Link to={item?.link} target="_blank" className="mb-0 text-ellipsis fw-bold text-body" title={item.postname}>{item?.postname || ''}</Link>
                                                        :
                                                        <span className="mb-0 text-ellipsis fw-bold text-body">{item?.postname || ''}</span>}
                                                    <div className="d-flex align-items-center">
                                                        <span className="mb-0 text-ellipsis fs-10 text-muted">@{parse(item.username)}</span>
                                                        <span className="h-4px w-4px rounded-circle mx-1 d-flex flex-shrink-0" style={{ backgroundColor: 'var(--bs-gray-600)' }}></span>
                                                        <span className="mb-0 text-ellipsis fs-10 text-muted"> {moment(new Date(item.createdAt * 1000)).fromNow()}</span>
                                                    </div>
                                                </div>
                                            </div>
                                            <span className="d-flex align-items-center h-24px w-24px flex-shrink-0">
                                                <Image src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.networkId)}.svg`)} height={24} width={24} alt={GET_NETWORK_NAME(item.networkId)} title={GET_NETWORK_NAME(item.networkId)} fluid />
                                            </span>
                                        </div>
                                    </section>
                                    <section className="d-flex align-items-center p-3 border-top">
                                        {item.status != 3 ? <div className="w-50 me-1">
                                            <Button variant='public' size='sm' onClick={this.clickToPostStatus(index, item.imageList?.length ? item.ptPost : item.postId, 1)} className={`${item.status == 1 ? 'disabled' : ''} w-100`} disabled={localStorage.getItem('wallLimitExceed') == 'true' ? true : false}>Public</Button>
                                        </div> : null}
                                        {item.status != 3 ? <div className="w-50 ms-1">
                                            <Button variant='private' size='sm' onClick={this.clickToPostStatus(index, item.imageList?.length ? item.ptPost : item.postId, 2)} className={`${item.status == 2 ? 'disabled' : ''} w-100`} disabled={localStorage.getItem('wallLimitExceed') == 'true' ? true : false}>Private</Button>
                                        </div> : null}
                                    </section>
                                </Card>
                            </ErrorFound>
                        </article>
                        : null
                }) : null
            }
            </Masonry>
            {modalData ?
                <ContentModal clickToPin={this.clickToPin} clickToGetUgc={this.clickToGetUgc} clickToDeletePost={this.clickToDeletePost} clickToBlockUser={this.clickToBlockUser} modalCallback={this.handleCallback} modalToggle={showModal} modalData={modalData} exportDonwloadMedia={this.exportDonwloadMedia} slideAssets={this.slideAssets} currentIndex={this.state.currentIndex} filter={filter} handleAllImageError={handleAllImageError} onRefreshPost={this.onRefreshPost} />
                : null}
            <ChangeOrderModal openpinToTopModal={openpinToTopModal} closeModalPop={() => this.setState({ openpinToTopModal: false })} />
        </div>)
    }
}

const mapStateToProps = (state) => {
    return {
        isEditing: state.moderation?.isEditing,
        ugcData: state.moderation?.ugcData,
        ugcPopup: state.moderation?.ugcPopup,
        ugcRequestData: state.moderation?.ugcRequestData,
        moderation: state.moderation,
        limits: state.limitExceed
    }
}
const mapDispatchToProps = dispatch => {
    return {
        clickToPostStatus: (index, ptPost, id, value) => dispatch(clickToPostStatus(index, ptPost, id, value)),
        clickToPostPin: (index, postId, status) => dispatch(clickToPostPin(index, postId, status)),
        clickToDeletePost: (item, index, navigate) => dispatch(clickToDeletePost(item, index, navigate)),
        clickToBlockUser: (index, userName) => dispatch(clickToBlockUser(index, userName)),
        getUgcDetails: (data) => dispatch(GET_UGC_POST_DETAIL(data)),
        ugcModal: (state) => dispatch(ugcModal(state)),
        UPDATE_INSTA_POST_ACTION: (obj) => dispatch(UPDATE_INSTA_POST_ACTION(obj)),
        GET_UGC_REQUEST: (data) => dispatch(GET_UGC_REQUEST(data))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(MasonryView);
